import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.2 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>2. Placing the Instrument</h2>
			<ul>
				<li>This section provides information that helps you positioning the
				instrument in the laboratory.</li>
				<li>CAUTION <br/>
				Heavy Objects. The mass spectrometer might move uncontrollably
				and cause injuries. Wear steel-reinforced safety shoes and gloves during
				the installation.</li>
				<li>Tip Because of its weight, the DFS mass spectrometer might get
				stacked when there are grooves across the floor on the route of
				transport.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.1 Space and Load Requirements</h4>
			<ul>
				<li>The instrument has a maximum height of 140.6 cm (55.4 in.) and a
				maximum length of 156.8 cm (62.5 in.). The width for an instrument
				with one GC installed is 161.1 cm (63.5 in.), the width for an
				instrument with two GCs installed is 209.1 cm (82.4 in.). Figure 6-1
				shows a side view of the instrument, Figure 6-2 on page 6-4 shows a
				schematic top view for both configurations of the DFS mass
				spectrometer.</li>
				<li><img src="../images/operating/ch6_1.jpg" width="auto"  alt=''></img></li>
				<li><img src="../images/operating/ch6_2.jpg" width="auto"  alt=''></img></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.2 Instrument Footprint</h4>
			<ul>
				<li>In the laboratory, the position of your instrument should allow easy
				access to all sides. Figure 6-3 shows the required minimum clearance
				(footprint) for the DFS system. In addition, consider the layout when all
				system components are present (data system, recirculating chiller).</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.3 Floor Conditions</h4>
			<ul>
				<li>The floor of your laboratory should be level (unevenness less than
				10 mm) and able to carry the weight of the installed DFS mass
				spectrometer including all components of approximately 900 kg
				(2000 lb). Also consider the weight of any option that is added to the
				system. The approximate weights of the components are shown in
				Table 6-1 on page 6-4.</li>
				<li><img src="../images/operating/ch6_3.jpg" width="auto"  alt=''></img></li>
				<li><img src="../images/operating/ch6_4.jpg" width="auto"  alt=''></img></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.4 Load Distribution</h4>
			<ul>
				<li>During transport, the instrument is moved on three rolls. After the
				installation, the total weight of the instrument with analyzer, magnet,
				and gas chromatograph is distributed to five height-adjustable feet.
				Figure 6-3 on page 6-5 shows the load for each support point. The
				weight is distributed either on the three rolls or on the feet. The green
				numbers show the load distribution during transport, whereas the red
				numbers show the load distribution after the installation.</li>
				<li></li>
			</ul>

		</div>
	)
}
