import React from 'react';
import '../../styles/styles.css'

function _2_8() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_8;

function MainContents() {
	const title = '2-8. Electrostatic Sector';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>8. Electrostatic Sector</h2>
			<ul>
				<li>This section describes the electrostatic sector that is within the
				condenser housing. A detailed description of the collector and amplifier
				system is given in Ion Detection on page 2-67</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>8.1 Mechanical Components in the Condenser Housing</h4>
			<ul>
				<li>Figure 2-48 shows a schematical view of the condenser housing. It is
				mounted on top of a cast plate and consists of a monoblock vacuum
				chamber that contains all focusing and detection elements. On the
				bottom, flanges serve for connecting the analyzer TMP and the Analyzer
				Penning gauge. See Figure 2-20 on page 2-24. Accessible on the right
				side is a detachable mount for the SEM collector.</li>
				<li>NOTICE All parts within the condenser housing except the collector slit and the
				SEM system are specially adjusted. Do not loosen them! For
				instructions about replacing the SEM system, see Chapter 8, User
				Maintenance.</li>
				<li><img src="../images/operating/ch2_54.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>8.2 Electrostatic Analyzer</h4>
			<ul>
				<li>The electrostatic deflection field extends between the two plates of the
				electrostatic analyzer (ESA). It is generated by an electric toroidal
				condenser. To force the ions on the main circular path with the nominal
				deflection radius, the deflection voltage Uesa must be applied to the
				ESA condenser plates. This deflection voltage is internally calculated
				and generated by the ESA supply board. The board additionally supplies
				the operating voltage for the hexapole and the beam rotation
				quadrupole 2. See Figure 2-49.</li>
				<li>NOTICE Both deflection plates are precisely adjusted with respect to each other
				and to the bottom of the condenser housing. This adjustment is
				performed by special tools, which are not supplied with the system.
				Therefore, do not loosen any screws at the ESA unit. Readjustment
				can only be made by Thermo Fisher Scientific personnel!</li>
				<li>For stable ion operations, the accelerating voltage is coupled to the ion
				deflection voltage. The ratio of both voltages is selected such that all
				ions of selected ion energy can pass the electrostatic sector. For
				metastable ion operations, both voltages are decoupled and separately
				controlled by the scan processor.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>8.3 Electric Fields and Supply</h4>
			<ul>
				<li>Figure 2-49 gives a schematical overview about those parts of the
				DFS mass spectrometer that use electric fields to focus and accelerate
				ions. It also shows the printed circuit boards that supply these parts.</li>
				<li><img src="../images/operating/ch2_55.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>


		</div>
	)
}
