import React from 'react';
import '../../styles/styles.css'

function _2_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_2;

function MainContents() {
	const title = '2-2. Instrument Overview';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}

			<h2>2. Instrument Overview</h2>
			<ul>
				<li>Some elements for controlling important system functions are on the
front side and the rear side of the instrument. They are described in the
topics that follow.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.1 Instrument Front Side</h4>
			<ul>
				<li>Figure 2-2 shows the front side of the DFS mass spectrometer. The
					optional gas chromatograph stands on the table at the right front side.
					The source head with ion source, vacuum locks, and reference/reagent
					gas inlet system is on the left part of the instrument. The separating
					magnet is in the central tower of the instrument. The condenser housing
					with electrostatic sector and ion collector is on top of the right
					instrument part (analyzer cabinet).</li>
				<li><img src="../images/operating/ch2_2.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.1.1 Shock-proof Platform</h5>
			<ul>
				<li>Source head, magnet, and condenser housing are mounted on a cast
					plate. This integrated shock-proof platform provides isolation from
					floor vibration.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.1.2 System Control Panel</h5>
			<ul>
				<li>The system control panel is at the front side of the instrument, on top of
				the source head. See Figure 2-3.</li>
				<li><img src="../images/operating/ch2_3.jpg" width="auto"  alt=''></img><br/></li>
				<li>The control panel receives the status information from the power
						distribution board. Thus, if a problem occurs in the various modules,
						then this will be indicated at the control panel. Table 2-1 explains the
						function of the various LEDs.</li>
				<li>Tip If problems occur during the operation, then check the control
				panel first to find the source of the problem.</li>
				<li><img src="../images/operating/ch2_4.jpg" width="auto"  alt=''></img><br/></li>
				<li>In the upper area of the system control panel, LEDs indicate various
				system status or activities. See Figure 2-4. Below, a two-colored LED
				chain is a coarse pressure indicator for the source forevacuum. The
				bottom area of the panel contains two pairs of buttons for controlling
				the pumps and the vacuum locks.</li>
				<li><img src="../images/operating/ch2_5.jpg" width="auto"  alt=''></img><br/></li>
				<li>The system control panel contains four buttons with corresponding
				status LEDs. See Table 2-2.</li>
				<li><img src="../images/operating/ch2_6.jpg" width="auto"  alt=''></img><br/></li>
				<li>With the Exchange Locks buttons, you can control the valves of both
				vacuum locks. The LEDs indicate the current vacuum status of the lock
				areas and also give information about their operating status.</li>
				<li>With the Pumps buttons, you can switch the pumps of the respective
				instrument regions independently of each other. When you switch off
				the pumps of one instrument part (the source region, for example), the
				separation valve is closed automatically. Thus, it keeps the vacuum in
				the other part (the analyzer region, for example). The corresponding
				region is vented automatically after approximately 30 minutes. The
				LEDs indicate the current status of the pumps in both instrument
				regions.</li>
				<li>When both regions are at 80% of their operating vacuum or better, the
				instrument automatically opens the separation valve. Additionally, the
				color of the Vacuum LED in the top row of the system control panel
				changes to green.</li>
			</ul>

			<hr className='class-2'/>
			<h5>2.1.3 Reference Gas Inlet</h5>
			<ul>
				<li>Reference compounds are introduced into the ion source through the
				reference and reagent gas inlet below the system control panel. See
				Figure 2-5. Two adjusting knobs allow controlling the inflow of each gas
				type (reference gas on the left, and CI reagent gas on the right). Between
				the knobs is the reference injection port. It contains a septum, through
				which the reference sample is injected.</li>
				<li>For a detailed description of this inlet system, see Reference and
				Reagent Gas Inlet on page 2-31. Inlets for the CI reagent gases are on
				the power column. See Gas Inlets on page 2-14.</li>
				<li><img src="../images/operating/ch2_7.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.2 Rear Instrument Side</h4>
			<ul>
				<li>On the rear instrument side, the power column contains additional
				control elements, power outlets and other external connections. See
				Figure 2-6.</li>
				<li><img src="../images/operating/ch2_8.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.1 Power Control LEDs</h5>
			<ul>
				<li>In addition to the system status LEDs on the system control panel (see
				Figure 2-4 on page 2-7), the DFS mass spectrometer has three power
				control LEDs above the fuses. See Figure 2-7. They indicate whether the
				corresponding fuse is closed and the respective parts of the instrument
				have power. (See Table 2-3.)</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.2 Fuses</h5>
			<ul>
				<li>Six fuses (circuit breakers) are in the center of the control panel. See
				Figure 2-7. Table 2-3 shows the parts of the DFS mass spectrometer that
				are protected by the respective fuse. Power control LEDs indicate the
				correct function of the fuses F1F3 below (F1 corresponds to L1, for
				example).</li>
				<li><img src="../images/operating/ch2_9.jpg" width="auto"  alt=''></img><br/></li>
				<li><img src="../images/operating/ch2_10.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.3 Main Power Switch</h5>
			<ul>
				<li>Turn the main power switch 90 clockwise to switch on or
				counterclockwise to switch off the instrument (see Figure 2-8). Placing
				the main power switch in the Off position switches off all power to the
				DFS mass spectrometer, including the vacuum pumps. It also switches
				off the power to any device (GCs, autosampler, or ESI) that is connected
				to the instrument. See below.</li>
				<li><img src="../images/operating/ch2_11.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.4 Ports for Peripherals </h5>
			<ul>
				<li>In the middle of the power column, the instrument provides various
				power outlets for peripheral devices. See Figure 2-9. Here, you can also
				connect the peripherals to enable the DFS mass spectrometer to
				communicate with them.</li>
				<li><img src="../images/operating/ch2_12.jpg" width="auto"  alt=''></img><br/></li>
				<li>Ports on the power column allow DFS mass spectrometers that are
				shipped with the ESI option to communicate with safety-relevant
				ESI components like pumps and printed circuit boards.
				The analog input as well as the digital input and output can be used
				according your individual requirements. For example, connect the
				handshake cables of the GCs to the digital input port.</li>
				<li>NOTICE </li>
				<li>Do not use these ports to connect devices to the instrument that are
				not described in this manual.
				Unless the label of a port indicates otherwise, all ports are designed for
				a maximum voltage of 24 V.</li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.5 External Connections</h5>
			<ul>
				<li>The external connections for Ethernet communication with data
				system, line power, pumps exhaust, gases, and cooling water are at the
				bottom of the power column. See Figure 2-10. The exhaust hose from
				the rotary pumps is led through a feedthrough and connects the pumps
				to the exhaust system in the laboratory. Next to the Ethernet connector,
				a bolt provides a functional earth connection for the ground wires of
				peripheral devices.</li>
				<li><img src="../images/operating/ch2_13.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.6 Gas Inlets</h5>
			<ul>
				<li>Use the inlet port on the top left position of the gas inlets (pos. 8) to
				connect the compressed air supply of the laboratory. See page 2-72 for
				more information. Use the inlet port on the top right position (pos. 1)
				to connect the nitrogen gas supply of the laboratory. See page 2-70 for
				more information.</li>
				<li>When you use another gas to vent the DFS mass spectrometer, connect
				the respective gas supply to the nitrogen inlet port.</li>
				<li><img src="../images/operating/ch2_14.jpg" width="auto"  alt=''></img><br/></li>
				<li>The left side of the middle row (pos. 7) contains a gas inlet for helium
				that is used for GC carrier gas. If a GC is connected to the DFS mass
				spectrometer, then you can connect the helium gas pipe of the GC to
				the gas outlets on both sides of the source head, below the GC table. See
				Figure 2-56 on page 2-75. The auxiliary gas inlet port in the same row
				(pos. 2) is connected to a gas pipe (length 50 cm) that ends in the
				forepumps cabinet. This gas inlet is intended to be used with an
				installed ESI option. The spare gas inlet on the right side of the middle
				row (pos. 3) is connected to a gas pipe that leads to spare gas outlet ports
				next to the helium outlet ports for the GC. See Figure 2-56.</li>
				<li>The bottom row contains inlets designed for the CI reagent gases. The
				gas pipe coming from the CI gas 1 port (pos. 6) is led to the CI gas inlet
				on the right side of the reference gas inlet. In the standard version of the
				DFS mass spectrometer, the CI gas 2 pipe (pos. 5) is not connected the
				reference and reagent gas inlet. To use two CI gases, the instrument
				must be equipped with an Advanced CI kit. The CI gas 2 pipe is then
				connected to an extension of the reference and reagent gas inlet. The
				spare CI gas inlet port (pos. 4) is connected to a gas pipe (length 50 cm)
				that ends in the forepumps cabinet. This gas pipe is intended for future
				use. See Reference and Reagent Gas Inlet on page 2-31 for more
				information.</li>
				<li></li>
				<li></li>
			</ul>

		</div>
	)
}
