import React from 'react';
import '../../styles/styles.css'

function _3_4() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _3_4;

function MainContents() {
	const title = '3.4 DCI Probe with DIP-HT Device';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>4. DCI Probe with DIP-HT Device</h2>
			<ul>
				<li>The DIP-HT device is an optional accessory to the DCI sample probe.
				It replaces the DCI probe tip and a small inner ceramic tube. On its
				front side, the DIP-HT tip contains a small ceramic oven with a wire
				clamp. The sample is deposited in a special quartz crucible. The crucible
				is then inserted into the front hole of the oven and held by the clamp.
				See Figure 3-11 and Figure 3-12.</li>
				<li><img src="../images/operating/ch3_12.jpg" width="auto"  alt=''></img><br/></li>
				<li>The DCI probe with DIP-HT tip is longer than the tips of all other
				probes. Therefore, a plastic clamp must be clamped on the rod to stop
				the probe in the correct operating position. See page 7-64 for
				information.</li>
				<li><img src="../images/operating/ch3_13.jpg" width="auto"  alt=''></img><br/></li>
				<li>The maximum temperature of the DIP-HT crucible is approximately
				800 C. Heating-up from ambient temperature to 700 C lasts for
				approximately 1.5 minutes. The probe is not provided with a cooling
				device.</li>
				<li></li>
			</ul>

		</div>
	)
}
