import React from 'react';
import '../../styles/styles.css'

function _2_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_1;

function MainContents() {
	const title = '2-1. Introduction';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>1. Introduction</h2>

			  DFS     .        .

			<hr className='class-2'/>
			<h5>1.1 DFS      </h5>
			<ul> 
				<li>Quantification of Persistent Organic Pollutants (dioxins, pesticides,	etc.)</li>
				<li>Accurate Mass / Multiple Ionization Techniques</li>
				<li>Sports Doping Control</li>
			</ul>

			<hr className='class-2'/>
			<h4>1.2 Performance Specifications</h4>
			<ul>
				<li>The DFS mass spectrometer has these performance specifications:</li>
				<li>Signal-to-noise ratio: 20 fg TCDD: &gt; 200:1</li>
				<li>Quantification Dynamic Range: up to 106</li>
				<li>Mass Accuracy: &lt; 2 ppm peak matching<br/>
				&lt; 2 ppm ESCAN<br/>
				&lt; 5 ppm BSCAN</li>
				<li>Maximum Resolution: &gt;= 60000 (10% valley definition)</li>
				<li>Mass Range: 26000</li>
			</ul>

			<hr className='class-2'/>
			<h4>1.3 MS Configuration</h4>
			<ul>
				<li>The DFS mass spectrometer is a double-focusing sector-field instrument with reverse Nier-Johnson geometry. </li>
				<li>This means that the ions traverse
				the separating magnetic field before they enter the electric sector. </li>
				<li>Figure 2-1 shows a schematical top view of the analyzer system, which
				demonstrates the geometry of the instrument.</li>
				<li>Only the magnetic sector acts as mass separator whereas the electric sector has no mass dispersion. </li>
				<li>In contrast to this, both sectors have an
				energy dispersion as mentioned below. </li>
				<li>Like the magnetic sector, the electric sector acts as an ion optical lens:</li>
				<li>It focuses the intermediate
				image of the magnetic sector to the collector (exit) slit. </li>
				<li>As can be concluded from above, the magnetic and the electric sectors in
				combination act as an angular focusing device that focuses the ion source slit to the collector slit.</li>
				<li>Both the source slit and the collector slit define the resolution of the
				mass spectrometer.</li>
				<li>That means that the resolution is selected by setting
				the slit widths to the applicable values.</li>
				<li>The deflection radii and angles of the electric and of the magnetic sector,
				as well as the image and the object distances, are selected in such a way
				that the energy dispersion of the magnetic sector is inversely
				proportional to the energy dispersion of the electric sector.</li>
				<li>Thus, the
				combined system functions not only for angular focusing, but also for
				energy focusing.</li>
				<li>It is, therefore, called a double focusing mass
				spectrometer.</li>
				<li>Because the electric sector succeeds the magnetic sector,
				the geometry is called a reverse geometry.</li>
				<li><img src="../images/operating/ch2_1.jpg" width="auto"  alt=''></img><br/></li>
				<li>After the ions have passed the electric sector (ESA), the conversion
				dynode converts the ions to electrons. The resulting electron current is
				amplified by the secondary electron multiplier (SEM).</li>
				
			</ul>
			<hr className='class-2'/>
			<h5>1.3.1 Instrument Control and Data Exchange</h5>
			<ul><li>The DFS system is mainly operated from the desktop computer (data
system). The data system contains the software for setting and retrieving
instrument parameters. In addition to data handling and mass
spectrometer control, it stores the acquired mass spectral data. For the
offline evaluation of the data and other tasks, additional software is
delivered with the instrument.</li>
				<li>Tip : The software package that is shipped with the DFS mass
spectrometer contains backup software and a USB stick. Use these
items to perform a monthly backup of the data system.</li>
				<li>The data system uses an Ethernet cable for all communication between
the user and the internal computer (frontend) of the DFS system. The
microprocessor of the frontend controls all functions of the instrument.
Mass spectrometer components like ion source, separating magnet,
electrostatic sector, ion collector, and inlet systems are electrically
supplied by the printed circuit boards in the instrument.</li>
				
			</ul>

			<hr className='class-2'/>
			<h4>1.4 Sample Processing</h4>
			<ul>
				<li>The DFS mass spectrometer can be equipped with systems for analyzing
samples in various physical conditions. It also provides equipment for
several ionization modes. For a detailed description of the inlet systems,
see Reference and Reagent Gas Inlet on page 2-31.</li>
				<li>An optional vacuum lock is provided for the introduction of three types
of evaporation probes. For a detailed description, see Chapter 3, Direct
Evaporation System.</li>
				<li>Gas chromatographs (GC) can be used together with the DFS mass
spectrometer. In the standard configuration of the mass spectrometer,
the GC table is on the right side of the source head. See Figure 2-2 on
page 2-5. The height-adjustable GC table can be moved horizontally to
disconnect a mounted GC. If the mass spectrometer is to be used with
two GCs (Dual GC option), then a similar GC table can be mounted
on the left side of the source head. Chapter 4, GC/MS Interface
describes the function of the GC/MS Interface.</li>
			</ul>

		</div>
	)
}
