import React from 'react';
import '../../styles/styles.css'

function _2_7() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_7;

function MainContents() {
	const title = '2-7. Magnetic Sector';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>7. Magnetic Sector</h2>
			<ul>
				<li>The magnetic field of the separating magnet is used to analyze the ions
				with respect to their momentum. Because the accelerating voltage
				assures that all ions have the same kinetic energy, this results in a
				separation by mass.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>7.1 Magnetic Field</h4>
			<ul>
				<li>The separating magnetic sector field is generated by a closed shape
				electromagnet. The ions, when entering the magnetic sector, are forced
				on a circular path. The radius of this path depends on the ion mass to
				charge ratio m/z, the accelerating voltage Ua and the magnetic field
				strength B. When setting the accelerating voltage Ua (in volts), the ion
				mass (in mass units) and the magnetic field strength B (in Tesla), the
				radius rm of the mean ion path can be calculated in millimeters with this
				formula:</li>
				<li><img src="../images/operating/ch2_52.jpg" width="auto"  alt=''></img><br/></li>
				<li>The accelerating voltage Ua is applied between the ion source base plate
				(ground) and the ion volume. This voltage can be set to values of up to
				5000 V (full accelerating voltage). The accelerating voltage is created by
				the Unit power control 5 kV board. The magnetic field strength can be
				set to a maximum value of approximately 1.5 Tesla.</li>
				<li>The maximum magnetic field strength and the maximum accelerating
				voltage of 5000 V provide a mass range of 2 to 1200 units. As can be
				seen and calculated from the formula above, reducing the accelerating
				voltage can extend this mass range. Thus, a mass range of approximately
				6000 units can be obtained when reducing the accelerating voltage to
				1000 V.</li>
			</ul>

			<hr className='class-2'/>
			<h5>7.1.1 Scanning Modes</h5>
			<ul>
				<li>Setting the system to a certain mass or scanning through a mass range
				can be accomplished by these methods:</li>
				<li>Setting or scanning either the magnetic field at constant accelerating
				voltage,</li>
				<li>Setting or scanning the accelerating voltage at constant magnetic
				field,</li>
				<li>Using a combined mode (linked scans).</li>
				<li>For detailed information about the scanning modes, refer to the
				DFS Software Manual.</li>
			</ul>


			<hr className='class-2'/>
			<h4>7.2 Magnet Supplies</h4>
			<ul>
				<li>The magnetic field is formed in the pole gap of an electromagnet. The
				magnet current is supplied by the field regulator board. A field probe is
				in the pole gap of the separating magnet. The field regulator board is
				controlled by the scan instrument control board as can be seen from
				Figure 2-47. The magnetic field and scan parameters are set and
				observed with the DFS Tune software. Refer to the DFS Software
				Manual for more information.</li>
				<li><img src="../images/operating/ch2_53.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>7.2.1 Field Calibration</h5>
			<ul>
				<li>The magnetic calibration of the DFS mass spectrometer is based on the
				readback of the magnetic field. See Figure 2-47. The calibration is very
				stable and usually lasts for several months. One calibration is sufficient
				for all:</li>
				<li>Scan speeds,</li>
				<li>Scan ranges,</li>
				<li>Ionization modes, and</li>
				<li>Polarities.</li>
			</ul>

			<hr className='class-2'/>
			<h4>7.3 Deflecting Magnet</h4>
			<ul>
				<li>The deflecting magnet is mounted on a cast plate and consists of a
				closed, laminated iron core, which surrounds the laminated pole shoes.
				Both pole shoes are provided with Rogowski-type edges and curved
				entrance and exit surfaces.</li>
			</ul>

			<hr className='class-2'/>
			<h5>7.3.1 Coil Units</h5>
			<ul>
				<li>The two coil units enclose the pole shoes. They consist of the magnet
				current coils, the feedback coils for the field regulator and the cooling
				pipes. The magnet current coils are tapped and can be switched with the
				DFS Tune software either for large mass range or for small mass range.
				Refer to the DFS Software Manual for more information.</li>
			</ul>

			<hr className='class-2'/>
			<h5>7.3.2 Coil Cooling </h5>
			<ul>
				<li>The cooling pipes cool the magnet coils during operation. They are
				connected to the cooling water circuit of the mass spectrometer. A
				temperature probe at the exit pipe checks the cooling water temperature
				and stops the field regulator board if the temperature exceeds
				approximately 50 C. See Figure 2-55 on page 2-74 for a schematic of
				the cooling water circuit.</li>
			</ul>

			<hr className='class-2'/>
			<h5>7.3.3 Field Probe</h5>
			<ul>
				<li>The field probe, which provides the field signal for field regulation, is
				inserted into the pole gap. See Figure 2-47 on page 2-63 for a schematic
				of the magnetic field control circuit.</li>
				<li>The field probe is connected to the cooling water circuit. A clamp made
				of heat-transferring material fixes the water hose to the probe.</li>
				<li>Tip The position of the field probe has been optimized by the Thermo
				Fisher Scientific field service engineer during the installation.
				Therefore, mark the probe position before you remove it. Optimizing
				the probe position demands for experience and should be performed
				by a service engineer, if possible.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>7.4 Hum Compensation</h4>
			<ul>
				<li>The hum compensation coils are externally on the flight tube, just in
				front of the magnet pole gap. The unit compensates alternating
				magnetic fields of 50/60 Hz that might cause distortions of the peaks.
				The hum compensation circuit is on the field regulator board.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

		</div>
	)
}
