import React from 'react';
import '../../styles/styles.css'

function _2_6() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_6;

function MainContents() {
	const title = '2-6. Ion Optics';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>6. Ion Optics</h2>
			<ul>
				<li>This section describes the ion optics of the DFS mass spectrometer. It
				provides a short review of the ion path inside the mass analyzer. The ion
				optical elements will be described in sufficient detail to permit an
				understanding of the analyzer function. Theoretical considerations
				concerning ion optics are beyond the scope of this manual. Figure 2-46
				shows a schematic of the ion optics.</li>
				<li><img src="../images/operating/ch2_51.jpg" width="auto"  alt=''></img><br/></li>
				<li>See Figure 2-49 on page 2-66 for a schematical overview about those
				parts of the DFS mass spectrometer that supply electrical fields to focus
				and accelerate ions.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.1 Ion Volume</h4>
			<ul>
				<li>Ions are generated in the ion volume1 inside the ion source. The ions are
				extracted from the ion volume, focused, and accelerated by the ion
				source lens system (lens stack). When passing the ion source base plate,
				the ions have gained the full ion energy corresponding to the applied
				accelerating voltage Ua. See Ion Source and Source Supplies on
				page 2-38 for more information.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.2 Collision Cell </h4>
			<ul>
				<li>If the system is provided with the optional collision cell for collision
				activation (CA) experiments in the first field free region (FFR1), then
				this cell is between the base plate of the ion source and the ion source
				slit. See Figure 2-46. The region between the ion source slit and the
				magnetic sector is part of the first field free region (FFR1). The total
				FFR1 extends from the base plate to the entrance of the magnetic field.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.3 Ion Source Slit </h4>
			<ul>
				<li>In the basic system, the ion source (entrance) slit follows the base plate
				of the ion source. The slit is enclosed by the slit housing, which is
				mounted to the inner rear side of the source head. The housing is
				aligned to the source head by an accurately machined recess and an
				alignment pin. The ion source slit is screwed to the inner rear side of the
				slit housing. The bottom side of the slit housing contains a two-pin
				feedthrough unit for the operating voltage of the slit unit.</li>
				<li>In principle, the ion source slit and the collector (exit) slit are of similar
				design. The beam rotation quadrupole 1 is attached to the exit side of
				the ion source slit. See below. Both slits are controlled by the piezo
				electric effect, that is the slit widths are set by voltages applied to piezo
				electric elements. The voltages are supplied by the slits/conversion
				dynode/SEM board. By changing these voltages with the DFS Tune
				software, you can set the widths of both slits to values between 0 and
				0.6 mm. These slit settings define the resolution of the system. Refer to
				the DFS Software Manual for more information.</li>
				<li>When the ion source slit becomes defective (after several years of use),
				ask a Thermo Fisher Scientific field service engineer to replace it.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.4 Beam Rotation Quadrupole 1 </h4>
			<ul>
				<li>After the ions have passed the source slit, a beam rotation quad
				electrically rotates the ion beam. See Figure 2-46 on page 2-57. Thus it
				allows aligning the vertical beam plane parallel to the plane of the
				magnetic field. These alignments are mandatory to get maximum
				resolution and optimum peak shape. The DC voltage for this
				quadrupole is supplied by the slits/conversion dynode/SEM board. The
				voltage is set with the DFS Tune software and must be adjusted for high
				resolution. Refer to the DFS Software Manual for more information.</li>
				<li>A second beam rotation quadrupole is in front of the collector slit. See
				page 2-60.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.5 Focusing Quadrupole </h4>
			<ul>
				<li>The focusing quadrupole (focus quad) corrects the overall focus length
				of the analyzer system with respect to the location of the collector slit.
				The DC voltage for this quadrupole is supplied by the slits/conversion
				dynode/SEM board. The quadrupole voltage is set with the DFS Tune
				software. Refer to the DFS Software Manual for more information.</li>
				<li>Tip The focus quad voltage has been adjusted in the factory. Readjust
				it for tuningespecially when you change the ionization mode.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.6 Separation Valve1 </h4>
			<ul>
				<li>After passing the focus quad, the ion beam traverses the separation valve
				between source head and analyzer. See page 2-22 for details.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.7 Aperture </h4>
			<ul>
				<li>A beam-defining aperture is at the entrance side of the flight tube of the
				magnetic sector. See Figure 2-46. The flight tube unit consists of a
				narrow tube that is inside the magnet pole gap.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.8 Hum Compensation </h4>
			<ul>
				<li>The coils of the hum compensation device compensate the hum that
				possibly might be on the beam. See page 2-64 for detailed information.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>6.9 Magnetic Sector Field </h4>
			<ul>
				<li>When entering the magnetic sector, the ions are forced on a circular
				path. The radius of this path depends on the ion mass-to-charge
				ratio m/z, the accelerating voltage Ua and the magnetic field strength B.
				The magnetic sector is used for ion deflection and mass separation. See
				Magnetic Sector on page 2-62 for detailed information about the
				magnet.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.10 Energy Quadrupole </h4>
			<ul>
				<li>The energy quadrupole (energy quad) in the condenser housing
				optimizes the energy focus of the ions with respect to the location of the
				collector slit. The DC voltage for this quadrupole is supplied by the
				slits/conversion dynode/SEM board and controlled by the instrument.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.11 Energy Aperture</h4>
			<ul>
				<li>This aperture is at the place of the intermediate image plane and shields
				ions that are not properly deflected by the magnetic field. Like the
				condenser housing, it is set to ground potential.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.12 Hexapole</h4>
			<ul>
				<li>The hexapole controls the shape of the ion beam. The voltage is
				supplied by the ESA supply board and controlled by the instrument.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.13 Herzog Apertures </h4>
			<ul>
				<li>These apertures on both sides of the ESA shield the electrostatic field of
				this component. Like the condenser housing, they are set to ground
				potential.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.14 Electric Sector Field </h4>
			<ul>
				<li>The electric sector field forces the ions on the main circular path with
				the nominal deflection radius. It is generated by applying the deflection
				voltage Uesa to the ESA shield plates. See Electrostatic Sector on
				page 2-65 for detailed information about the ESA.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.15 Beam Rotation Quadrupole 2</h4>
			<ul>
				<li>Between the ESA and the collector slit (see Figure 2-49 on page 2-66),
				the ion beam passes the beam rotation quad 2. It allows aligning the
				vertical beam plane with respect to the plane of the collector slit. This
				beam rotation quad is similar to the quad next to the source slit. In
				contrast to the beam rotation quad 1, the DC voltage for this quad is
				supplied by the ESA supply board. The voltage is set with the DFS Tune
				software and must be adjusted for high resolution. Refer to the
				DFS Software Manual for more information.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.16 Collector Slit</h4>
			<ul>
				<li>In principle, the collector (exit) slit is of similar design as the source slit.
				(See page 2-58.) The slit width is changed electrically. The slit operating
				voltage is supplied by the slits/conversion dynode/SEM board. The slit
				width is set and displayed with the DFS Tune software. Refer to the
				DFS Software Manual for more information.</li>
				<li>Tip To replace the collector slit, it is necessary to open the lid of the
				condenser housing. Therefore, we recommend that you call a Thermo
				Fisher Scientific field service engineer to perform this maintenance
				task.</li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.17 Conversion Dynode </h4>
			<ul>
				<li>After the ions traverse the collector slit, they are deflected to the 20 kV
				conversion dynode. The particles generated during conversion
				(secondary electrons from positive ionspositive ions from negative
				ions) are accelerated to the first dynode of the SEM (secondary electron
				multiplier). For a detailed description of the conversion dynode and the
				SEM, see Ion Detection on page 2-67.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			
			<hr className='class-2'/>
			<h4>6.18 Switching the Ion Mode </h4>
			<ul>
				<li>Switching over from positive to negative ion operation is accomplished
				with the DFS Tune software. Refer to the DFS Software Manual for
				more information. This switchover automatically changes the polarity of
				these items:</li>
				<li>The polarity of the accelerating voltage Ua</li>
				<li>The polarity of the deflection voltage Uesa</li>
				<li>The polarity of the separating magnetic field (for positive ions the
					upper pole is the south pole)</li>
				<li>The polarity of the conversion dynode voltage</li>
			</ul>
		</div>
	)
}
