import React from 'react';
import '../../styles/styles.css'

function _5_7() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_7;

function MainContents() {
	const title = '5.7 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>7. Residual Hazards</h2>
			<ul>
				<li>Users of the DFS mass spectrometer must pay attention to these residual
				hazards.</li>
				<li>WARNING <br/>
					Explosion Hazard. When you use hydrogen, methane, or any
					flammable gas as CI reagent gas, these gases might form explosive
					mixtures with air. Be careful when you operate in the presence of
					flammable gases and fluids! Beware of leaks!<br/>
					Flammable gases (for example, hydrogen or methane) are not
					allowed for usage as carrier gases with the DFS mass spectrometer!</li>
				<li>WARNING <br/>
				High Voltage. Electric Shock Hazard. During operation, the inner
				parts of the ion source cable and ion source feedthroughs, the ion
				source electrodes and the ion source wiring are set to high voltage
				(5 kV). Switch off the accelerating voltage and the Protected
				Electronics for maintenance and repair! Before you switch on the
				Protected Electronics and the accelerating voltage, make sure that all
				parts under high voltage are covered and protected properly!</li>
				<li>WARNING <br/>
				High Voltage. Electric Shock Hazard. During operation, the direct
				probes, their cabling, and the supply circuits are on 5 kV accelerating
				voltage. This voltage is very dangerous! Make sure that the
				yellow/green ground cables of the probes are connected to ground!<br/>
				For maintenance and repair, switch off the accelerating voltage, take
				the probes out of the vacuum lock, and disconnect the cabling! After
				maintenance and repair, make sure that all parts at high voltage are
				covered and protected properly.The green/yellow ground cable must
				be connected to the main chassis (ground)!</li>
				<li>WARNING <br/>
				High Voltage. Electric Shock Hazard. The accelerating and the
				deflection voltages are very dangerous! Do not touch any part of the
				ESA when the instrument is switched on.</li>
				<li>WARNING <br/>
				High Voltage. Electric Shock Hazard. The conversion dynode
				voltages and the SEM voltages are very dangerous! Switch off these
				voltages for maintenance and repair! After maintenance and repair,
				make sure that all parts at high voltage are covered and protected
				properly!</li>
				<li>WARNING <br/>
				High Voltage. Electric Shock Hazard. Parts of the printed circuit
				boards are at high voltage. Opening the electronics cabinet is only
				allowed for maintenance purposes by qualified personnel. Always
				switch off power before you perform operations at the electronics!</li>
				<li>CAUTION<br/>
				Hazardous Chemicals. Samples and solvents might contain toxic,
				carcinogenic, mutagenic, or corrosive/irritant chemicals. Avoid
				exposure to potentially harmful materials. Always wear protective
				clothing, gloves, and safety glasses when you handle solvents or
				samples. Also contain waste streams and use sufficient ventilation.
				Refer to your supplier's Material Safety Data Sheet (MSDS) for correct
				handling of a particular compound.
				</li>
				<li>CAUTION<br/>
				Hazardous Chemicals. When you introduce hazardous compounds
				into the system, they will contaminate the pumps, the vacuum parts,
				the ion source parts, etc. and will be dissolved in the pump oil! Use
				only locally-approved containers and procedures for waste oil disposal!
				Contaminated pumps must be emptied and all contaminated pumps
				and parts must be decontaminated before they are sent back to
				Thermo Fisher Scientific for repair or exchange!
				</li>
				<li>CAUTION<br/>
				Hazardous Chemicals. When the instrument is analyzing hazardous
				materials, these might be present in the effluent of the forepumps! The
				connection to an adequate exhaust system, therefore, is mandatory!
				</li>
				<li>CAUTION<br/>
				Hot Surface. The central block, the injection port, and the cap screw
				are at operating temperature. They might cause severe burns if you
				touch them. Treat heated zones with respect. Let hot components cool
				before you service them.
				</li>
				<li>CAUTION<br/>
				Hot Surface. A running forepump is hot and some surfaces could
				reach a temperature higher than 80 C (176 F). Touching the parts of
				a forepump might cause burns. Switch off the pump and let it cool
				down before any intervention or take applicable precautions. Always
				wear heat protective gloves when you work on a pump that is still
				warm from operation.
				</li>
				<li>CAUTION<br/>
				Hot Parts. Parts of the GC, the transfer line, and the source head and
				analyzer tube might be hot. Respect heated zones! Let heated
				compounds cool down before you handle or disassemble them!
				</li>
				<li>CAUTION<br/>
				Hot Parts. In case of failure of the water circuits, all parts of the water
				distribution unit might be considerably heated up. Do not touch the
				parts! Before you disconnect the cooling water hoses, make sure that
				the cooling water has cooled down!
				</li>
				<li>CAUTION<br/>
				Strong Magnet. Strong magnetic fields are used in the instrument,
				which can be harmful to pacemaker wearers and others with medical
				implants. Keep tools and other metal objects away. The magnet might
				pull them out of your hand or cause you to lose your balance and fall.
				Do not bring compressed gas cylinders within close proximity to the
				instrument. To avoid damage, keep computers, credit cards, and other
				magnetically sensitive devices away from the instrument.
				</li>
				<li>Notice on Lifting and Handling of Thermo Scientific Instruments
				For your safety, and in compliance with international regulations, the
				physical handling of this Thermo Scientific instrument requires a team
				effort for lifting and/or moving the instrument. This instrument is too
				heavy and/or bulky for one person alone to handle safely.
				</li>
			</ul>

		</div>
	)
}
