import React from 'react';
import '../../styles/styles.css'

function _2_11() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_11;

function MainContents() {
	const title = '2-11. Cooling Water Circuit';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>11. Cooling Water Circuit</h2>
			<ul>
				<li>The DFS mass spectrometer uses water for cooling these instrument
				parts:</li>
				<li>Magnet</li>
				<li>Turbomolecular pumps</li>
				<li>Magnetic field probe</li>
				<li>DI devices or ESI</li>
				<li>Figure 2-55 shows a schematical view of the cooling water circuit in the
				DFS mass spectrometer. The recirculating chiller provides cooling water
				at a temperature of 20 C. It enters and leaves the instrument at the
				bottom of the power column. See Figure 2-10 on page 2-14.</li>
				<li><img src="../images/operating/ch2_61.jpg" width="auto"  alt=''></img><br/></li>
				<li>From the cooling water inlet port, the fresh water is led to a distributor
				block at the rear side of the DFS mass spectrometer, next to the magnet.
				From here, the fresh water is led to the magnet coils and the field probe.
				A clamp made of heat-transferring material fixes the water hose to the
				probe. From the distribution block, the water passes through the
				turbomolecular pumps in the sequence Source TMP  Analyzer
				TMP 2  Analyzer TMP 1. Afterwards, it leaves the instrument
				through the cooling water outlet port and returns to the recirculating
				chiller.</li>
				<li>To prevent the cooling water hoses from
				being accidentally disconnected from the
				DFS mass spectrometer, locking plates fix
				them to the connectors at the instrument.
				See photo right. After the hoses have been
				plugged into the instrument, the locking
				plates are attached to both hose
				connections.</li>
				<li><img src="../images/operating/ch2_62.jpg" width="auto"  alt=''></img><br/></li>
				<li>Tip Do not forget to install the locking plates again if you need to
				reconnect the cooling water hoses (after you have performed
				maintenance, for example)!</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>11.1 Cooling of DI Devices or ESI</h4>
			<ul>
				<li>When you use direct evaporation probes or ESI, the additional cooling
				water circuit is connected as shown in Figure 2-55. The cooling water
				ports for these devices are on both sides of the source head, next to the
				GC table(s). Figure 2-56 shows the ports on the left side of the source
				head.</li>
				<li><img src="../images/operating/ch2_63.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>11.2 Recirculating Chiller</h4>
			<ul>
				<li>As option, a Thermo Scientific ThermoFlex 900 recirculating chiller can
				be shipped with the instrument. It makes the DFS mass spectrometer
				independent from any cooling water supply. See Cooling Water on
				page 6-19 for details.</li>
			</ul>


		</div>
	)
}
