import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.5 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>5. Cooling Water</h2>
			<ul>
				<li>Optionally, the DFS mass spectrometer can be delivered with a Thermo
				Scientific ThermoFlex 900 recirculating chiller with closed circuit,
				cooled by a refrigerating device. The chiller makes the mass
				spectrometer independent from any cooling water supply.</li>
				<li><img src="../images/operating/ch6_7.jpg" width="auto"  alt=''></img></li>
				<li>Use a wall outlet to provide the electric power for the chiller. Two water
				hoses (black), internal diameter 9 mm, wall thickness 3 mm, length
				approximately 3 m (~10 ft) are shipped with the instrument. The hoses
				are connected to the ports at the rear side of the MS.</li>
				<li>For instruction about performing maintenance for the chiller, refer to
				the manufacturers manual for the chiller.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>5.1 Properties of Cooling Water</h4>
			<ul>
				<li>The water temperature is not critical, but should be in the range of 20 to
				25 C (68 to 77 F). Set the internal temperature regulation of the
				chiller to 20 C. Lower temperatures could lead to a condensation of
				atmospheric water vapor. Also, high humidity in the laboratory might
				lead to condensation.</li>
				<li>We recommend that you use distilled water rather than de-ionized water
				because it contains lower concentration of bacteria and residual organic
				matter. The water should be free of suspended matter to avoid clogging
				of the cooling circuit. In special cases, we recommend that you install an
				in-line filter to guarantee consistent water quality. The cooling water
				should meet these requirements:</li>
				<li><img src="../images/operating/ch6_8.jpg" width="auto"  alt=''></img></li>
			</ul>

		</div>
	)
}
