import React from 'react';
import '../../styles/styles.css'

function _5_6() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_6;

function MainContents() {
	const title = '5.6 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>6. In Case of Emergency</h2>
			<ol>
				<li>To shut down the system in case of emergency</li>
				<li>Turn the main power switch 90 counterclockwise to switch off the
				instrument. (See Figure 5-3). The power column is on the power
				column at the rear side of the mass spectrometer (see Figure 5-2).
				Placing the main power switch in the Off position switches off all
				power to the DFS mass spectrometer, including the vacuum pumps.
				It also switches off the power to any device (GCs, autosampler, or
				ESI) that is connected to the instrument.<br/>
				<img src="../images/operating/ch5_10.jpg" width="auto"  alt=''></img><br/>
				<img src="../images/operating/ch5_11.jpg" width="auto"  alt=''></img>
				
				</li>
				<li>Switch off the computer with its On/Off switch.</li>
				<li>WARNING<br/>
				 Electric Current. Electric Shock Hazard. Capacitors inside the
				instrument might still be charged even if the instrument is switched
				off. Wait for sufficient time before you remove the instrument
				housing.</li>
			</ol>

		</div>
	)
}
