import React from 'react';
import '../../styles/styles.css'

function _2_10() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_10;

function MainContents() {
	const title = '2-10. Gas Distribution';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>10. Gas Distribution</h2>
			<ul>
				<li>The DFS mass spectrometer needs a minimum of two gases for its
				operation:</li>
				<li>Vent gas (recommended)</li>
				<li>Compressed air</li>
				<li>The inlet ports for both gases are at the bottom of the power column at
				the rear side of the instrument. See Figure 2-11 on page 2-15. After the
				gases have entered the instrument, they are led to the pressure
				regulators.</li>
				<li>Tip Instead of using compressed air, you can connect the compressed
				air inlet port to the vent gas supply.</li>
				<li>The pressure regulators for both gases are on top of the forepumps
				cabinet. See Figure 2-21 on page 2-25. The vent gas pressure regulator is
				on the left side and the compressed air pressure regulator is on the right
				side. See Figure 2-52.</li>
				<li><img src="../images/operating/ch2_58.jpg" width="auto"  alt=''></img><br/></li>
				<li>Tip Both pressure reducers have been adjusted to their respective
				operating pressures by the factory. The pressure values are indicated by
				labels next to the pressure reducers and usually do not need to be
				changed. Each pressure reducer has been sealed by a counter nut.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>10.1 Vent Gas Distribution</h4>
			<ul>
				<li>The inlets of the vent valves at the source TMP, the analyzer TMP 1,
				and the vent ports of the vacuum locks are connected to a common vent
				gas pipe.</li>
				<li>The vent valves of the turbomolecular pumps open and vent the pump
				and the vacuum system in case of pump power failure or pump switch
				off. This is necessary to prevent forepump oil vapor and bearing
				lubrication oil vapor from migrating back into the vacuum system after
				the pump rotation has stopped. The vent gas flow through the valves is
				reduced by a throttle inside the valve.</li>
				<li>Venting with a dry and clean inert gas (usually nitrogen) avoids
				contamination of the vacuum system and increases the SEM lifetime.
				The vent gas supply must be connected to the vent gas port on the
				power column. See Figure 2-11 on page 2-15. In principle, it is possible
				to vent both parts of the vacuum system and the vacuum locks of the
				DFS mass spectrometer with air. But we recommend that you use a gas
				(usually nitrogen) for venting. Doing so prevents the vacuum system
				from contamination (with humidity, for example) and minimizes the
				evacuation time.</li>
				<li>Tip Instead of dry nitrogen, you can use dry helium or dry argon as
				vent gases.</li>
				<li><img src="../images/operating/ch2_59.jpg" width="auto"  alt=''></img><br/></li>
				<li>Figure 2-53 shows a schematic diagram of the vent gas distribution in
				the instrument. Coming from the vent gas supply of your laboratory, the
				vent gas enters the instrument at the bottom of the power column. See
				Figure 2-10 on page 2-14. The inlet pressure of the vent gas should be
				approximately 110 bar (14145 psi). A pressure regulator in the
				forepumps cabinet provides the operating pressure of 0.1 bar (1.4 psi).
				(See Figure 2-21 on page 2-25 and Figure 2-52 on page 2-70.)</li>
				<li>Tip The pressure reducer has been adjusted ready to use by the factory.
				This value is indicated by a label next to the pressure regulator and
				usually it does not need to be changed. The pressure reducer has been
				sealed by a counter nut.</li>
				<li>From there, the vent gas is led to the vent valves of the ion source TMP
				and the analyzer TMP 1. (See Figure 2-18 on page 2-21 and
				Figure 2-20 on page 2-24.) Then, it is led to the vent valves of the box
				exchange lock (See Figure 2-17 on page 2-20.) and the probe exchange
				lock. (See Figure 2-14 on page 2-18.)</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>10.1.1 Gases for Special Applications</h5>
			<ul>
				<li>The DFS mass spectrometer allows using additional gases for some
				applications:</li>
				<li>Helium is used as GC carrier gas.
				It enters the instrument at an inlet port on the power column (See
				Figure 2-11 on page 2-15.) and is led to outlet ports on both sides of
				the ion source head. See Figure 2-56 on page 2-75.</li>
				<li>Up to two gases are used for chemical ionization (hydrogen,
				methane, isobutane, ammonia)
				They enter the instrument at an inlet port on the power column
				(See Figure 2-11.) and are led to the reference and CI gas inlet on
				top of the source head. See Reference and Reagent Gas Inlet on
				page 2-31 for more information.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>10.2 Compressed Air Distribution</h4>
			<ul>
				<li>Figure 2-54 shows a schematic view of the compressed air circuit. The
				compressed air enters the instrument at the bottom of the power
				column. See Figure 2-10 on page 2-14. The inlet pressure of the
				compressed air should be approximately 610 bar (88145 psi). A
				sensor checks whether the inlet pressure of the compressed air is
				sufficient. If it is not, the DFS Tune software displays a warning
				message. Refer to the DFS Software Manual for more information. A
				pressure regulator in the forepumps cabinet provides the operating
				pressure of 5.86.2 bar (8490 psi). (See Figure 2-21 on page 2-25 and
				Figure 2-52 on page 2-70.)</li>
				<li>Tip The pressure reducer has been adjusted ready to use by the factory.
				This value is indicated by a label next to the pressure regulator and
				usually does not need to be changed. The pressure reducer has been
				sealed by a counter nut.</li>
				<li><img src="../images/operating/ch2_60.jpg" width="auto"  alt=''></img><br/></li>
				<li>Coming from the pressure regulator, the compressed air is buffered in a
				compressed air store. This device is in the forepumps cabinet. If the
				compressed air supply fails while you are working with the DFS mass
				spectrometer, then this buffer provides enough air for bringing the
				instrument to a stable state. The compressed air is then distributed for
				pneumatic control of these components:</li>
				<li>Separation valve (See page 2-22.)</li>
				<li>Box exchange lock (See page 2-55.)</li>
				<li>Probe exchange (DI) lock (See page 3-2.)</li>
				<li>Vacuum changeover valve (See page 2-22.)</li>
			</ul>

		</div>
	)
}
