import React from 'react';
import '../../styles/styles.css'

function _5_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_0;

function MainContents() {
	const title = '5. Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>
			
			{/*  */}
			<p>This chapter provides information that is important for your own safety
			or the safety of others, and that prevents damage to the instrument,
			Read this chapter carefully before you install or operate the instrument
			and its accessories, or come into contact with it.</p>
			<ul>Contents
				<li>Safety Symbols and Signal Words in this Manual</li>
				<li>Safety Symbols on the Instrument</li>
				<li>Intended Use</li>
				<li>Electric Safety Precautions</li>
				<li>Safety Instructions for Wearers of Medical Implants</li>
				<li>In Case of Emergency</li>
				<li>Residual Hazards</li>
				<li>Personal Protective Equipment</li>
			</ul>


		</div>
	)
}
