import React from 'react';
import '../../styles/styles.css'

function TargetQuan_7() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_7

function MainContents() {
	const title = 'Chapter 7 Response Page';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Response Page</h2>
			<h2>2. Spreadsheet Layout Wizard </h2>
				<h4>2.1 Response Graph Axis </h4>
				<h4>2.2 Response Columns </h4>
				<h4>2.3 Mathematic Columns </h4>
				<h4>2.4 Layouts </h4>
				<h4>2.5 Response Parameters</h4> 
			<h2>3. Entry View</h2>
			<h2>4. Response Graph</h2>
			<h2>5. Details List</h2>
    </div>


  )
}
