import React from 'react';
import '../../styles/styles.css'

function _5_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_2;

function MainContents() {
	const title = '5.2 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>2. Safety Symbols on the Instrument</h2>
			<ul>
				<li>Table 5-1 lists the safety labels on the instrument. See the indicated
				safety notices to prevent risk of harm to the operator and to protect the
				instrument against damage. Read and obey the instructions on the
				labels.</li>
				<li><img src="../images/operating/ch5_1.jpg" width="auto"  alt=''></img><br/></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.1 Name Plate</h4>
			<ul>
				<li>To correctly identify the instrument when communicating with Thermo
				Fisher Scientific, always have the information from the name plate (also
				called rating plate) available. The name plate is attached to the power
				column at the rear side of the instrument. See Figure 5-1. It contains the
				serial number, which is important in any type of communication with
				Thermo Fisher Scientific. Especially, the serial number is needed to
				access the SharePoint of the Bremen Technical Documentation group.
				See Contacting Us on page 1-5.</li>
			</ul>

		</div>
	)
}
