import React from 'react';
import '../../styles/styles.css'

function _3_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _3_3;

function MainContents() {
	const title = '3.3 DCI Probe';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			{/*  */}
			<hr className='class-1'/>
			<h2>3. DCI Probe</h2>
			<ul>
				<li>The DCI (Direct Chemical Ionization) direct evaporation probe
				originally was intended for flash evaporation in the chemical ionization
				modebut it can also be used for flash evaporation in the electron
				impact (EI) ionization mode. The very rapid evaporation in many cases
				prevents sample decomposition.</li>
				<li>The DCI probe has an insulated tip, the probe rod with the handle, and
				the sealing insert with sealing bush. The electrical supply wires lead
				from the socket in the handle through the rod to the insulated tip and
				end in a small socket for the evaporation wire units. See Figure 3-9.</li>
				<li><img src="../images/operating/ch3_10.jpg" width="auto"  alt=''></img><br/></li>
				<li><img src="../images/operating/ch3_11.jpg" width="auto"  alt=''></img><br/></li>
				<li>The evaporation wire units (DCI wire units) are inserted into the socket
				at the front of the probe tip. The dissolved sample is to be dropped from
				a syringe into the loop of the wire. After evaporating the solvent, the
				DCI sample probe is to be introduced into the source block. With the
				DFS Tune software, you can set and read temperature values and edit,
				start, stop, and interrupt temperature programs. Refer to the
				DFS Software Manual for more information. The DCI probe is not
				cooled.</li>
				<li>During operation, the tip of the DCI probe is introduced into the inlet
				hole of the ion source block and the front end of the wire unit is
				positioned in front of the ionization region. The sealing surface at the
				front of the removable probe tip is pressed against the outer sealing
				cylinder of the ion source block. (See also Ion Source and Source
				Supplies on page 2-38.) Thus, the line is sealed and you can use the
				direct probe for EI as well as for CI operation.</li>
				<li>Tip Lock out the probe before you change the ionization box!</li>
				<li>During operation, the insulated tip of the direct probe contacts the ion
				source. Therefore, the tip is connected to the 5 kV accelerating voltage
				when the probe is fully inserted into the ion source!</li>
			</ul>

		</div>
	)
}
