import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.6 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>6. Compressed Air</h2>
			<ul>
				<li>Compressed air with a pressure of 0.6 MPa (87 psi) is required to
				operate the pneumatic valves of the instrument. A suitable compressor is
				available from Thermo Fisher Scientific.</li>
				<li>Tip Do not use gas tanks for compressed air supply. The needed
				amounts of compressed air are much higher.</li>
				<li>Thermo Fisher Scientific recommends that you filter the compressed air
				to remove particulate matter and moisture. The main supply must be
				connected to the compressed air service unit of the DFS mass
				spectrometer with the provided PVC tubing 61 mm.</li>
				<li>Keep the access to the compressed air supply free at all times so that
				you can switch off the supply in case of a fracture of the supply line.</li>
			</ul>

		</div>
	)
}
