function Header() {
	const style = {
		fontSize: "25px",
		fontWeight: "bold",
		color: "#ddffaa"
  };
	
	const menuShow = (mItems) => { 
		return mItems.map( 
			(item, index) => { 
				if (item.submenu) { 
					return ( 
            <div>
              <NavDropdown title={ item.title } key={index} > {menuShow( item.submenu )} </NavDropdown> 
            </div>
					); 
				} else { 
					return ( 
						<Nav.Link href={ item.url } key={index} className='nav-link'> {item.title} </Nav.Link> 
					); 
				} 
			} 
		); 
	}; 

	return (
		<div>

			<nav className="navbar navbar-expand-md fixed-top bg-dark navbar-light" >
			 	<i className='fab fa-accusoft mx-2 fs-2 text-info' />
				<Link className="nav-link " to={"/"} style={style}>Thermo</Link>
				<button class="navbar-toggler" type="button" >
					<span class="navbar-toggler-icon"></span>
				</button>

					<Navbar.Collapse id="basic-navbar-nav"> 
						<Nav> 
							{menuShow( navBarData )} 
						</Nav> 
					</Navbar.Collapse> 

			</nav>

		</div>
  )
}

export default Header