import React from 'react';
import '../../styles/styles.css'

function _5_4() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_4;

function MainContents() {
	const title = '5.4 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>4. Electric Safety Precautions</h2>
			<ul>
				<li>WARNING</li>
				<li>High Voltage. <br/>
					High voltages that can cause an electric shock are used
				in the instrument. Observe these safety precautions when you operate
				or perform service on your instrument:</li>
				<li>Do not change the external or internal grounding connections.
				If you tamper with these connections or disconnect these
				connections, you could cause injuries to yourself and/or you could
				cause damage to the system.</li>
				<li>The instrument is grounded in accordance with regulations when
				shipped. You do not need to make any changes to the electrical
				connections or to the instruments chassis to ensure safe operation.</li>
				<li>Do not run the system without the housing on. Permanent
					damage can occur. When you leave the system, make sure that all
					protective covers and doors are properly connected and closed, and
					that heated areas are separated and marked to protect unqualified
					personnel.</li>
				<li>Do not start the instrument if you suspect that it has any kind of
				electrical damage. Instead, disconnect the power cord of the mass
				spectrometer and contact a Thermo Fisher Scientific field service
				engineer for a product evaluation. Do not try to use the
				instrument until it has been evaluated. Electrical damage might
				have occurred if the system shows visible signs of damage, or has
				been transported under severe stress.</li>
				<li>Do not place any objects upon the instrumentespecially not
				containers with liquidsunless it is requested by the user
				documentation. Leaking liquids might get into contact with
				electronic components and cause a short circuit.</li>
			</ul>

			<hr className='class-2'/>
			<h4>4.1 Installing a Residual Current Device in the Laboratory Building</h4>
			<ul>
				<li>WARNING</li>
				<li>Risk of Electric Shock in case of an Instrument driven with
				Cooling Water. Under extremely rare conditions, the cooling water
				supply inside the instrument might become leaky and water might
				come into contact with the mains voltage.</li>
				<li>The leakage of water could endanger personnel when they come into
				contact with the water and could lead to a life-endangering electric
				shock in the worst case scenario.</li>
				<li>If water is leaking from the instrument, do not touch the water or step
				into it. Do not touch the instrument except for the power off switch.
				Switch off the instrument immediately. Contact the local Thermo
				Fisher Scientific service or your local distributor.</li>
				<li>Thermo Fisher Scientific recommends installing a residual current
				device (RCD) in the laboratory building. A residual current device is
				also called Ground Fault Circuit Interrupter (GFCI). When it senses an
				imbalance between the outgoing and incoming current, this device
				instantly breaks the electric circuit to prevent serious harm from an
				outgoing electric shock. For further details, refer to the Technical
				Information of the residual current device.</li>
				<li>NOTICE</li>
				<li>For the installation of an RCD in the building, the local installation
				requirements must be followed.</li>
				<li>The instrument must be protected by an RCD with a tripping current
				of maximum 30 mA. This can be accomplished by installing an RCD in
				the laboratory building that fuses the power socket of the instrument. If
				this is not possible, Thermo Fisher Scientific can alternatively provide an
				in-line RCD box. In this case, contact your local service office.</li>
				<li>If an insulating type transformer, insulating type UPS or insulating type
				power conditioner is used, the RCD must be connected between the
				insulating device and the instrument. The in-line RCD box can be used
				here as well:</li>
				<li>in-line RCD box for North America</li>
				<li>in-line RCD box for other countries</li>
			</ul>

		</div>
	)
}
