import React from 'react';
import '../../styles/styles.css'

function TargetQuan_6() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_6
function MainContents() {
	const title = 'Chapter 6 Quantitation Page';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Quantitation Page </h2>
			<h2>2. Spreadsheet Layout </h2>
				<h4>2.1 Input Area of the Spreadsheet Layout</h4>
				<h4>2.2 Additional Value Dialog</h4>
			<h2>3. Spreadsheet Layout Wizard </h2>
				<h4>3.1 Show Entries Section</h4>
				<h4>3.2 Sort Entries Section </h4>
				<h4>3.3 Show Parameters Section  </h4>
				<h4>3.4 Layouts Section </h4>
			<h2>4. Application Properties</h2>
			<h2>5. Quan Parameters</h2>
			<h2>6. Entry Dependencies </h2>
				<h4>6.1 Showing Dependencies </h4>
				<h4>6.2 Fixed Order </h4>
			<h2>7. Chromatogram View</h2>
				<h4>7.1 Toolbar </h4>
				<h4>7.2 Spectrum Display </h4>
				<h4>7.3 Parameter View</h4>
			<h2>8. Response Graph</h2>
			<h2>9. Sequence View</h2>
    </div>


  )
}
