import React from 'react';
import '../../styles/styles.css'

function _4_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _4_2;

function MainContents() {
	const title = '4.2 GC/MS Interface';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>2. Components of the GC/MS Interface</h2>
			<ul>
				<li>The gas chromatograph is coupled to the ion source by means of an
				interface. The GC/MS interface consists basically of three main
				components:</li>
				<li>The column coupling,</li>
				<li>The GC flange assembly (see Figure 4-4 on page 4-5), and</li>
				<li>The transfer line (see Figure 4-5 on page 4-6 and Figure 4-6 on
					page 4-7).</li>
				<li>A schematic diagram of the GC/MS coupling is given in Figure 4-3.
				The GC coupling consists of the line-of-sight inlet, the transfer line and
				the coupling:</li>
				<li>The flange of the line-of-sight inlet is connected to the side of the
				ion source head. Usually the right side flange is used when a single
				GC is connected to the instrument.</li>
				<li>The line of-sight inlet contains a Cajon sealing (see Figure 4-4 on
					page 4-5) through which the transfer line is introduced.</li>
				<li>The transfer line is then kept in its operating position by spacers and
				an intermediate pipe.</li>
				<li>The transfer line protrudes through an opening in the wall into the oven
				of the gas chromatograph. It is not connected to the gas chromatograph.
				The electrical connection line to the heater and the temperature probe
				of the transfer line leads out of the oven. It is plugged into the socket on
				the outer side of the gas chromatograph.</li>
				<li><img src="../images/operating/ch4_4.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.1 GC Flange Assembly</h4>
			<ul>
				<li>Figure 4-4 shows a cross section view of the GC flange assembly. The
				GC flange assembly is screwed to the (usually right) side of the source
				head. The Cajon connector is screwed on the GC flange with
				connecting tube. The Cajon connector is a cap nut that is screwed to the
				connecting tube. When you screw on the Cajon connector, it
				compresses an o-ring that seals the smooth outer surface of the transfer
				line to the smooth inner surface of the connecting tube.</li>
				<li><img src="../images/operating/ch4_5.jpg" width="auto"  alt=''></img><br/></li>
				<li>The transfer line is introduced from the left side into the connecting
				tube. To hold the source side of the transfer line in the optimum
				operating position, spacers are used to adjust the distance between
				transfer line and ion source block. Optimum operating position means
				that the tip of the transfer line is centered with respect to the outer
				sealing insert of the ion source and that this sealing insert is
				spring-pressed to the smooth surface of the ceramic insulator tip of the
				transfer line. This can be seen in more detail from Figure 4-5.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.2 Transfer Line</h4>
			<ul>
				<li>CAUTION The transfer line and the ion source are hot during operation. Before
				you touch them, switch them off and let them cool down. See
				Shutting Down the GC Transfer Line of the DFS System on
				page 7-68 for instructions.</li>
				<li>Figure 4-5 shows how the transfer line with the interface capillary is
				coupled to the ion source. The ion source contains on the GC side a set
				of inner and outer sealing inserts, which are pressed in opposite
				direction by a spring. The outer insert seals to the tip of the transfer line,
				the inner insert seals to the ionization box. The inserts are very
				similarbut not identicalto the inserts for the direct evaporation
				probe on the opposite side of the ion source.</li>
				<li>In the Dual GC Configuration of the DFS mass spectrometer, the ion
				source has GC feedthroughs on both sides. See page 4-9.</li>
				<li><img src="../images/operating/ch4_6.jpg" width="auto"  alt=''></img><br/></li>
				<li>The transfer line mainly consists of three concentric tubes. The outer
				tube is provided with a very smooth surface and serves for sealing by the
				Cajon connector. It is vacuum tight welded to the front of the transfer
				line. The inner tube is also vacuum tight welded to the front of the
				transfer line. This tube leads the interface capillary from the column
				oven to the ion source.</li>
				<li>The space between the intermediate tubenot shown on the figures
				aboveand the inner tube is filled by a heat transfer rod, two cartridge
				heaters and a thermo probe. The heat transfer rod transports the heat
				from the heater to any location of the inner tube and thus provides a
				homogeneous heating along the interface capillary.</li>
				<li>The electrical connection wires to the heater and the thermo probe leave
				the transfer line on the column side and end in plug 451.</li>
				<li>Using the DFS Tune software, the inner tube with the interface capillary
				can be either set to a discrete temperature value in the range of up to
				approximately 340 C, or it can be programmed for temperature scans
				with temperatures up to approximately 340 C.</li>
				<li>NOTICE Thermo Fisher Scientific advises against using temperatures higher
				than 340 C!</li>
				<li>A ceramic insulator with a longitudinal channel for the interface
				capillary and a lateral hole for pumping insulates the transfer line from
				the ion source, which is on accelerating voltage during operation. An
				intermediate pipe connects the ceramic insulator and the transfer line.</li>
				<li>The transfer line must be grounded during operation. The transfer line
				is automatically grounded as soon as plug 451 is inserted into its socket
				in the gas chromatograph, and the gas chromatograph is grounded with
				its main plug.</li>
				<li>Figure 4-6 shows the GC side of the transfer line with the coupling part.
				The inner tube of the transfer line protrudes out of the transfer line. It is
				connected and sealed to the coupling part by an 1/8 cap nut with
				graphite ferrule (Swagelok system). The interface capillary is connected
				and sealed to the coupling part by a slotted cap nut with graphite sealing
				unit. All parts shown in Figure 4-6 are in the GC oven, that is the
				transfer line protrudes into the oven.</li>
				<li><img src="../images/operating/ch4_7.jpg" width="auto"  alt=''></img><br/></li>
			</ul>

			<hr className='class-2'/>
			<h5>2.2.1 Direct Coupling</h5>
			<ul>
				<li>In case of Direct Coupling, the interface capillary is identical with the
				chromatographic column (fused silica column).</li>
				<li>The direct coupling device consists of the column end, the coupling
				part, and the transfer line. The MS end of the transfer line protrudes
				into the source head and contacts with its ceramic insulator the outer
				sealing insert of the ion source. (See Figure 4-5 on page 4-6.) The
				GC end of the transfer line protrudes into the GC oven and is there
				connected to the coupling part. (See Figure 4-6.)</li>
				<li>The column end (fused silica column) is introduced into the coupling
				part and traverses the coupling part, the transfer line, the ceramic
				insulator, the outer sealing insert, and a part of the inner sealing insert.</li>
				<li>(See Figure 4-5 on page 4-6.) It must end 18.5 mm behind the ceramic
				insulator in front of the ionization box of the ion source. Thus the
				ionization boxes of the ion source can be changed without moving the
				column end. The column end is connected and sealed to the coupling
				part by a graphite sealing unit. (See Figure 4-6.)</li>
				<li>With the direct coupling, the total effluent of the GC column is directly
				introduced into the ion source. Direct coupling is the standard coupling
				method with the DFS mass spectrometer.</li>
			</ul>

		</div>
	)
}
