import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.3 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>3. Laboratory Conditions</h2>
			<ul>
				<li>This section gives an overview of important requirements for the
				laboratory where the DFS mass spectrometer is placed. For details, refer
				to the DFS Pre-Installation Requirements Guide.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.1 Electric Power Supply</h4>
			<ul>
				<li>The basic power requirements for a DFS system are as follows:</li>
				<li>Nominal voltage of 230 V AC, 10%, frequency of 50/60 Hz<br/>
				Three phases, 5-wire system in Y configuration (neutral wire
				connected to earth).<br/>
				For the instrument, one wall receptacle, each phase fused with 32 A
				(30 A in the United States)</li>
				<li>Additional single-phase wall outlets (for computer, monitor and
				printer)<br/>
				Nominal voltage 230 V 10%, 50 or 60 Hz AC, fused with 10 A</li>
				<li>The system must have an earth ground hard-wired to the main panel.
				The interconnected power outlets for the system must have a common
				point to one ground connector. If there are two such points, each of
				which is connected to separate external ground, then they will cause
				noise current to flow through the ground system by the ground loop
				that is formed.</li>
				<li>Notice for US customers. <br/>
					The 230 V AC is measured to ground and
				not phase to phase, because the DFS system requires a Y phasing with
				400 V AC between phases. An isolation transformer is required that
				steps up the three phases 120 V AC to 230 V AC with respect to
				ground. This isolation transformer also provides a transformation
				from  to Y phasing. A suitable isolation transformer is available from
				Thermo Fisher Scientific.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>3.1.1 Power Cables and Connectors</h5>
			<ul>
				<li>The power cord to the DFS mass spectrometer is 5 m (16 ft) long. The
				cords from the computer, monitor and printer are approximately 2 m
				(6 ft) long. The instrument is shipped with the 32 A version of a 5-pole
				CEE male connector, which is rated at 332 A and 230 V AC. The
				female wall receptacle is supplied by Thermo Fisher Scientific in
				advance. See page 6-21. It is your responsibility to have the receptacle
				installed by your electrician before the instrument installation can begin.</li>
				<li>Local codes in your area may require another type of plug and receptacle
				be installed. The Thermo Fisher Scientific field service engineer for your
				country will provide the applicable power plugs.</li>
				<li>Notice for US customers. <br/>
				In the US, the instrument is hard-wired to
				the isolation transformer.</li>
			</ul>

			<hr className='class-2'/>
			<h5>3.1.2 Power Supply for Peripheral Devices</h5>
			<ul>
				<li>The power column at the rear side of the instrument provides power
				outlets for peripheral devices such as GCs, autosampler, or
				ESI forepump. Use wall outlets for the recirculating chiller and all other
				additional devices. See also Power Supply for GC/MS Systems on
				page 6-23.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.2 Laboratory Temperature </h4>
			<ul>
				<li>The instrument is designed to operate at the temperatures specified in
				Table 6-2.</li>
				<li><img src="../images/operating/ch6_5.jpg" width="auto"  alt=''></img></li>
				<li>The air conditioning load (average power dissipation during analysis
				operation) for a basic DFS system, including a gas chromatograph and
				the data system, is approximately 4.7 kW (4.5 BTU/s). For the dual
				GC configuration, the average air conditioning load amounts
				approximately to 4.9 kW (4.7 BTU/s).</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>3.3 Humidity</h4>
			<ul>
				<li>The relative humidity of the operating environment must be between
				30 and 70%, with no condensation. Thermo Fisher Scientific
				recommends that your laboratory be equipped with a
				temperature/humidity monitor. This makes sure that your laboratory is
				always within the required temperature and humidity specifications.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.4 Exhaust System </h4>
			<ul>
				<li>Vacuum and solvent wastes must be vented separately, and wastes must
				be collected and disposed correctly.</li>
				<li>Much of what is injected into the gas chromatograph will eventually be
				exhausted from the GC and the rotary vane pumps of the mass
				spectrometer. Additionally, the pumps will emit small amount of oil
				vapor.</li>
				<li>An efficient fume exhaust system is required for the correct operation of
				the fore pumps. The exhaust port of the rotary vane pumps and the
				GC outlets should be connected to an fume exhaust gas line that leads
				out of the building or exhaust system. The minimum inner diameter of
				the pipe should be 25 mm (1 in.). The exhaust system for the fore
				pumps must be able to accommodate a flow rate of 1 L/min.</li>
				<li>If you intend to analyze poisonous samples, then install filters into the
				gas exhaust system that surely prevent poisons from being emitted into
				the atmosphere. Obey local regulations strictly!</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.5 Vibration</h4>
			<ul>
				<li>Floors must be free of vibration that is caused, for example, by
				equipment in adjoining locations.</li>
				<li>The DFS system has standard vibration damping equipment that damps
				vibrations with frequencies of more than 15 Hz with amplitudes up to
				1 mm/s2. This is sufficient to allow the correct function of the system in
				a normal laboratory environment.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.6 Particulate Matter</h4>
			<ul>
				<li>The air in your laboratory must not contain excessive dust, smoke, or
				other particulate matter. For reference, the air should contain fewer than
				100000 particles per cubic foot in excess of 5 m.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.7 Radio Frequencies</h4>
			<ul>
				<li>The DFS system can withstand electromagnetic fields of 10 V/m in the
				frequency range between 26 MHz and 1 GHz without any influence on
				operation.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.8 Magnetic Interferences</h4>
			<ul>
				<li>The installation room must be free of interfering magnetic fields. The
				system has standard compensation equipment for alternating magnetic
				fields of 50/60 Hz and 150/180 Hz. Under the conditions normally met
				in modern laboratory buildings, this will suffice to compensate the
				present alternating magnetic fields. No compensation is provided for
				interference fields of other frequencies or varying DC magnetic fields,
				which may occur in exceptional cases.</li>
				<li>Disturbing fields can also be produced by other mass spectrometers that
				are in operation with magnetic scan in a circumference of less than
				10 m.</li>
			</ul>

		</div>
	)
}
