import React from 'react';
import '../../styles/styles.css'

function _3_2() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _3_2;

function MainContents() {
	const title = '3.2 DI Probe';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>
			{/*  */}
			<h2>2. DI Probe</h2>
			<ul>
				<li>The operating principle of the DI probe is as follows: a heater, a cooling
				circuit, and a temperature probe build up a regulation unit. This unit
				permits setting of discrete temperature values or programming of
				specific temperature courses.</li>
				<li>The DI probe contains heater, cooler, and temperature probe in the rod
				section of the probe on the low voltage side. The heat is transferred to
				the crucible oven at the tip of the probe by an insulator. In operating
				position, the probe tip contacts the ion source and is thus set to the 5 kV
				accelerating voltage. The heater is on the much lower potential of the
				regulation circuit and the cooler is on ground potential. This leads to a
				much more economic construction but decreases the regulation speed
				and precision.</li>
				<li><img src="../images/operating/ch3_5.jpg" width="auto"  alt=''></img><br/></li>
				<li>The sample is deposited in crucibles. They are inserted into the front of
				the probe tip and fixed by a clamp. See Figure 3-6.</li>
				<li><img src="../images/operating/ch3_6.jpg" width="auto"  alt=''></img><br/></li>
				<li>During operation, the crucible oven is introduced into the inlet hole of
				the ion source block and the front end of the crucible is positioned in
				front of the ionization region. The sealing surface at the probe tip is
				pressed against the outer sealing cylinder of the ion source block. (See
				also Ion Source and Source Supplies on page 2-38.) Thus, the line is
				sealed and you can use the direct probe for EI as well as for
				CI operation.</li>
				<li><img src="../images/operating/ch3_7.jpg" width="auto"  alt=''></img><br/></li>
			</ul>

			<hr className='class-2'/>
			<h5>0.1.1 Available Crucible Types</h5>
			<ul>
				<li><img src="../images/operating/ch3_8.jpg" width="auto"  alt=''></img><br/></li>
				<li><img src="../images/operating/ch3_9.jpg" width="auto"  alt=''></img><br/></li>
				<li>Usually, aluminum crucibles are used. The crucibles will be disposed off
				after use. We recommend that you clean them before application: boil
				them several times in an ultrasonic bath of acetone, then dry them
				carefully, and afterwards store them in a glass container.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>2.1 Cooling of the DI Probe</h4>
			<ul>
				<li>Cooling of the DI probe is provided by the cooling water circuit. The
				blue plastic hoses for cooling water of the DI probe end in special plugs,
				which are to be inserted into corresponding sockets of the general
				cooling water circuit of the instrument. These sockets are at the left and
				right side of the source head, below the GC table. See Figure 3-3 on
				page 3-5. See also Cooling Water Circuit on page 2-74 for more
				information.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

		</div>
	)
}
