import React from 'react';
import '../../styles/styles.css'

function _2_9() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_9;

function MainContents() {
	const title = '2-9. Ion Detection';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>9. Ion Detection</h2>
			<ul>
				<li>After passing the resolution-defining collector slit, the ions enter the
				detection system. See Figure 2-50. The mechanical parts of the ion
				detection system are inside the condenser housing. The detection system
				consists of the conversion dynode, the secondary electron multiplier
				(SEM), and a Faraday collector cup.</li>
				<li><img src="../images/operating/ch2_56.jpg" width="auto"  alt=''></img><br/></li>
				<li>The detection system is of the off-axis typeif the operating voltages of
				the conversion dynode and of the SEM are switched off, then the ions
				pass straight through the system and hit the Faraday collector.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>9.1 Conversion Dynode</h4>
			<ul>
				<li>The incoming ion beam is deflected onto the conversion dynode and
				converted on its surface. Positive ions generate electrons, negative ions
				are converted into positive ions. The converted particles are accelerated
				by the electric field that extends between the conversion dynode and the
				SEM system.</li>
				<li>During normal operations, the conversion dynode is set to an operating
				voltage of 1020 kV. This voltage is automatically set to negative
				polarity if the spectrometer is set to positive ions and to positive polarity
				for negative ions. This 20 kV voltage is supplied by the 20 kV power
				supply board. The voltage is switched on or off, or set to different
				polarities (for service purposes) with the DFS Tune software. Refer to
				the DFS Software Manual 	for more information.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>9.2 Secondary Electron Multiplier (SEM)</h4>
			<ul>
				<li>The basic process in a secondary electron multiplier (SEM) is the
				emission of secondary electrons after electrons striking the surface. The
				electrons enter the SEM and hit the first (of seventeen) dynode, where
				they release secondary electrons. Each dynode focuses these secondary
				electrons onto the next dynode, which in turn emits even more
				secondary electrons.</li>
				<li>In this way, a cascade of electrons is developed between successive
				dynodes. Each dynode increases the number of electrons in the cascade
				by a factor &gt; 2 until the cascade of electrons reaches the output
				electrode. Here, the signal is extracted. Figure 2-51 shows the principle
				of the cascading process.</li>
				<li>Each electron cascade in the SEM generates an electrical pulse that is
				counted with digital counter/timer electronics. The number of events in
				a given time slice is called count rate [cps]. Intensities of mass peaks are
				measured in cps (counts per second) if an ion counting system is used.
				The signals are processed in the SEM preamplifier (see below).</li>
				<li><img src="../images/operating/ch2_57.jpg" width="auto"  alt=''></img><br/></li>
				<li>The SEM operating voltage is supplied by the slits/conversion
				dynode/SEM board, which also supplies the operating voltages for the
				electrically adjustable slits and the rotation quads. (See Figure 2-49 on
				page 2-66.) You can set the SEM voltage to any value between 0 and
				3000 V with the DFS Tune software. Refer to the DFS Software Manual
				for more information.</li>
				<li>The SEM voltage is connected to the first dynode of the SEM. The
				voltage for the other 16 dynodes is taken from taps of a 17-stage voltage
				divider (seventeen 500 kiloohms resistors). The voltage divider extends
				between the first dynode and ground and is inside the SEM cover.</li>
				<li>Multipliers fatigue. Thus, from time to time you must increase the
				multiplier voltage to get the operating gain of approximately 106. By
				setting the SEM voltage with the DFS Tune software, you can control
				the gain factor. Refer to the DFS Software Manual for more information.
				After a longer period of operation (the time depends on the load), you
				must replace the SEM. See page 8-80 for instructions.</li>
				<li>The electron current is collected by the electron collector of the SEM
				and sent to the SEM preamplifier. The SEM preamplifier is a separate
				unit on the DFS amplifier board, which also contains the Faraday
				amplifier. The SEM preamplifier contains the DC amplifier and circuits
				for digitizing the signal. The internal variation of the initial sampling
				frequency leads to a very fine setting of the effective sampling
				frequency, which serves to display the peaks and the spectra on the
				screen.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>9.3 Faraday Cup</h4>
			<ul>
				<li>If the operating voltages of the conversion dynode and of the SEM are
				switched off, then the ions pass straight through the system and hit the
				Faraday cup. The Faraday cup itself is a hollow collector, opened at one
				side and closed at the other, used to collect the ion beam. The collected
				ions create a voltage, which is detected by the Faraday amplifier.</li>
				<li>Tip The Faraday cup is used only to assess the amplification factor of
				the SEM by comparing the signal intensities of both detection
				channels.</li>
				<li>The Faraday amplifier is a separate charge integration amplifier circuit
				on the DFS amplifier board, which also contains the SEM preamplifier.
				Electric power to the Faraday amplifier is supplied by the power
				distribution board.</li>
				<li></li>
			</ul>


		</div>
	)
}
