import React from 'react';
import '../../styles/styles.css'

function _4_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _4_3;

function MainContents() {
	const title = '4.3 GC/MS Interface';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>3. Dual GC Configuration</h2>
			<ul>
				<li>The Dual GC Configuration includes the DFS mass spectrometer with
				two TRACE 1310 GC units and a TriPlus RSH autosampler. See
				Figure 4-7. This system is designed for unattended operation, where the
				autosampler serves one or two TRACE GC units from shared sample
				trays. The GCs can be equipped with a wide range of injectors, from
				cold on-column and split/splitless injectors, going to advanced
				temperature programmed injectors (PTV).</li>
				<li>This instrument configuration can be used for these operations:</li>
				<li>Inject from one vial into two different GCs/columns</li>
				<li>Analyses on columns with different polarity within one sequence
				(for example, for confirmatory analysis according to EPA 1613)</li>
				<li>The second GC can serve as backup GC for extended uptime.</li>
				<li><img src="../images/operating/ch4_8.jpg" width="auto"  alt=''></img><br/></li>
				<li>The GCs are simultaneously installed on both sides of the same ion
				source for separation on two independent GC setups. The ion source
				has GC feedthroughs on both sides. The autosampler is delivered with
				an extended bar to serve two GCs.</li>
			</ul>


		</div>
	)
}
