import React from 'react';
import '../../styles/styles.css'

function _2_12() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_12;

function MainContents() {
	const title = '2-12. Printed Circuit Boards';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}

			<h2>12. Printed Circuit Boards</h2>
			<ul>
				<li>The DFS system is controlled by a PC, running an expanded version of
				the Xcalibur</li>
				<li>The electronics of the mass spectrometer contains complicated and
				numerous circuits. Therefore, only qualified and skilled electronics
				engineers should perform servicing. We recommend that you call a
				Thermo Fisher Scientific field service engineer if servicing is required.
				Before you call a service engineer, please try to localize the defect. A
				precise description of the defect will ease the repair and decrease the
				costs. We recommend that you use Thermo Fisher Scientific spare parts
				only, because many parts are specially selected.</li>
				<li>Tip You can test many of the instrument components with the
				diagnostic functions of the DFS Tune software. Refer to the
				DFS Software Manual or the Help for instructions.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>12.1 Protected Electronics</h4>
			<ul>
				<li>When the DFS mass spectrometer is in operating mode, various parts of
				the instrument are at high voltage and can be a danger to humans. To
				protect the user, these instrument parts must be switched off under
				certain conditions. Protected Electronics includes nearly the complete
				electronics for ion generation (for example, generation of potentials and
				high voltage), but without the power distribution board and the field
				regulator board. These boards run permanently and without high
				voltage.</li>
				<li>These instrument components are part of the Protected Electronics:</li>
				<li>Ion source supply,</li>
				<li>High voltage supplies (accelerating voltage, lens supply, conversion
					dynode voltage supply, ESA supply),</li>
				<li>Emission controller</li>
				<li>Heating of the reference and reagent inlet,</li>
				<li>All other heating and baking devices, and</li>
				<li>ESI (if installed)</li>
				<li>Other safety features of the DFS mass spectrometer include the vacuum
				protection (See Figure 2-22 on page 2-28.), the magnet cooling (See
				Coil Cooling on page 2-64.), and the door switches. The latter switch
				off the ESA supply board and the slits/conversion dynode/SEM board
				when one of the doors is opened.</li>
			</ul>

			<hr className='class-2'/>
			<h5>12.1.1 Switching off the Protected Electronics</h5>
			<ul>
				<li>In case of a vacuum failure, the power distribution board initiates an
				automatic power down of the instrument and switches off the Protected
				Electronics. See Vacuum Control on page 2-27 for more information.</li>
				<li>To perform maintenance (cleaning the ion source, for example), you
				must switch off the Protected Electronics manually with the DFS Tune
				software. Refer to the DFS Software Manual for more information.</li>
				<li></li>
				<li></li>
			</ul>
		</div>
	)
}
