import React from 'react';
import '../../styles/styles.css'

function _5_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_1;

function MainContents() {
	const title = '5.1 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>


			{/*  */}
			<h2>1. Safety Symbols and Signal Words in this Manual</h2>
			<ul>
				<li>Notices that concern the safety of the personnel who operate the
				DFS Magnetic Sector GC-HRMS system appear different from the
				main flow of text:</li>
				<li>Always be aware of what to do with and the effect of safety
				information.</li>
				<li>CAUTION Points out a hazardous situation that can lead to minor or medium
				injury if it is not avoided.</li>
				<li>WARNING Points out a hazardous situation that can lead to severe injury or death
				if it is not avoided.</li>
				<li>DANGER Points out a hazardous situation that will lead to severe injury or death
				if it is not avoided.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>1.1 Observing this Manual</h4>
			<ul>
				<li>Keep this manual always near the instrument to be available for quick
				reference.</li>
				<li>Be sure to read and comply with all precautions that are described in
				this manual.</li>
				<li>System configurations and specifications in this manual supersede all
				previous information received by the purchaser.</li>
			</ul>

		</div>
	)
}
