import React from 'react';
import '../../styles/styles.css'

function TargetQuan_5() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default TargetQuan_5
function MainContents() {
	const title = 'Chapter 5 Sequence Page';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			<h2>1. Sequence Page</h2>
			<h2>2. Setup Pane </h2>
			<h2>3. Sequence Table </h2>
			<h2>4. Output Pane </h2>
			<h2>5. Overview Pane </h2>
    </div>


  )
}
