import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6. Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>
			
			{/*  */}
			<p>This chapter describes the conditions for an operating environment that
			will ensure continued high performance of your DFS Magnetic Sector
			GC-HRMS system.</p>
			<ul>Contents
				<li>Safety Guidelines for Installation</li>
				<li>Placing the Instrument</li>
				<li>Laboratory Conditions</li>
				<li>Gases</li>
				<li>Cooling Water</li>
				<li>Compressed Air</li>
				<li>Setting Up the Instrument Hardware and Adjusting the System
				Parameters</li>
			</ul>
			<p>To be sure that your laboratory is ready for the installation of the DFS
			system, you have to meet all requirements specified in the
			DFS Pre-Installation Requirements Guide. This guide also provides
			comprehensive information to assist in planning and preparing your lab
			site.</p>
			<p>Only employees of Thermo Fisher Scientific or personnel acting on
			behalf of Thermo Fisher Scientific are allowed to install the DFS mass
			spectrometer.</p>

		</div>
	)
}
