import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.4 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>4. Gases</h2>
			<ul>
				<li>Your system can use large gas amounts during daily operation. The gases
				must be delivered with the necessary pressure and purity. Particulate
				filters can be a source of contamination and are not recommended. Store
				and handle all chemicals in accordance with standard safety procedures.</li>
				<li>All gas lines should be oil free and preferably flame dried. The gas lines
				should run to the rear side of the DFS mass spectrometer. Gas supply
				lines should terminate with 1/8-in. female Swagelok-type connectors.</li>
				<li>The gas lines should be:</li>
				<li>As short as possible and close to the DFS system.</li>
				<li>Made of copper or stainless steel when using helium, hydrogen,
				methane, and isobutane.</li>
				<li>Made of stainless steel when using ammonia or other corrosive gases.</li>
				<li>NOTICE <br/>
				Contaminations introduced during installation of house lines used for
				gas delivery can cause damage to the system. Make sure that all gas
				lines used with your system have been cleaned of all particulates and
				oils. You are responsible for any damage to the instrument that is
				caused by contaminations that are introduced from your gas delivery
				system. <br/>
				Do not store gas tanks where they can cause damage to cables or gas
				lines. Secure them in accordance with standard safety procedures.</li>
				<li>CAUTION <br/>
				Fire Hazard Some CI gases, such as methane and isobutane, are
				flammable. Make sure that these gases are sufficiently exhausted and
				that all gas fittings on the system are leak-free. Consult your local
				Environmental and Safety Regulations for information about how to
				exhaust fumes from your laboratory.</li>
				<li>WARNING <br/>
				Toxic Substances Hazard Some CI gases, such as ammonia, are toxic.
				Make sure that these gases are sufficiently exhausted and that all gas
				fittings on the system are leak-free. Consult your local Environmental
				and Safety Regulations for information about how to exhaust fumes
				from your laboratory.</li>
				<li>WARNING <br/>
				Fire Hazard When you use hydrogen, it is critical to test all
				connections, lines, and valves for leaks before using the instrument.
				When you perform maintenance, be sure to turn off the hydrogen
				supply.</li>
				<li><img src="../images/operating/ch6_6.jpg" width="auto"  alt=''></img></li>
			</ul>

			<hr className='class-2'/>
			<h4>4.1 Using Hydrogen</h4>
			<ul>
				<li>To safely use hydrogen in your DFS system, you should have a hydrogen
				sensor installed in your laboratory. The sensor must be calibrated
				occasionally, as described in the sensors documentation.</li>
				<li>Use these safety precautions when using hydrogen:</li>
				<li>Make sure that all hydrogen cylinders comply with the safety
				requirements for use and storage. Hydrogen cylinders and delivery
				systems must comply with local regulations.</li>
				<li>Make sure that the gas supply is turned completely off when you
				connect hydrogen lines.</li>
				<li>Perform a bubble test to ensure that the hydrogen lines are leak-tight
				before you use the instrument. Repeat this test to eliminate all leaks.</li>
				<li>Always turn off the GC and shut off the hydrogen at its source before
				you vent the MS.</li>
				<li>Remove as many sources of ignition as possible from your laboratory.
				Sources can include open flames, electrostatic discharges, or devices
				that spark.</li>
				<li>Do not open a cylinder of hydrogen without a regulator attached
				because it might self-ignite.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.1.1 Hydrogen Safety Precautions</h5>
			<ul>
				<li>Hydrogen is a colorless, odorless, highly flammable gas with the
				molecular formula H2 and an atomic weight of 1.00794, making it the
				lightest element. Hydrogen gas presents a hazard because it is
				combustible over a wide range of concentrations: at ambient
				temperature and pressure, this ranges from about 4% to 74.2% by
				volume.</li>
				<li>Hydrogen has a flash point of 253 C (423 F) and an auto-ignition
				temperature of 560 C (1040 F). It has a very low ignition energy and
				the highest burning velocity of any gas. If hydrogen is allowed to expand
				rapidly from high pressure, it can self-ignite. Hydrogen burns with a
				flame that can be invisible in bright light.</li>
				<li>Fire Hazard Hydrogen is potentially explosive and must be used with
				extreme care. Any use of hydrogen gas must be reviewed by
				appropriate health and safety staff and all installations of hydrogen
				systems must be performed to applicable codes and standards.</li>
				<li>Flammable gases (for example, hydrogen or methane) are not
				allowed for usage as carrier gases with the DFS mass spectrometer!</li>
				<li>Before you begin using hydrogen, you should conduct a risk assessment
				based on the quantity of hydrogen to be used and the conditions of your
				laboratory. You should ask yourself:</li>
				<li>What hydrogen hazards associated with this project are most likely
				to occur?</li>
				<li>What hydrogen hazards associated with this project have the
				potential to result in the worst consequences?</li>
				<li>Try to reduce or eliminate the higher risks by using the sufficient
				ventilation to remove hydrogen gas before an ignitable
				concentration can accumulate. You should also consider purging the
				hydrogen to further reduce hazards. Make sure that anyone who will
				be working with hydrogen has basic hydrogen safety training.</li>
				<li>As with laboratory safety in general, be sure to wear safety glasses,
				laboratory coats, gloves, etc. Typically there are no specific
				requirements for gaseous hydrogen, other than eye protection when
				working with a compressed gas. If you work with liquid (cryogenic)
				hydrogen, wear insulated gloves and protective shoes in addition to
				eye protection.</li>
				<li>You should post No Smoking and No Open Flames signs to
				identify hydrogen sources and cylinders. Maintain, inspect and
				leak-test all hydrogen sources regularly.</li>
				<li>All hydrogen shutoff valves should be clearly marked and permanent
				hydrogen piping should be labeled as such at the supply or discharge
				point and at regular intervals along its length. Where hydrogen gas
				piping passes through a wall, the piping should be labeled on both
				sides of the wall.</li>
				<li>There should also be contingency plans in place should an incident
				occur.</li>
				<li>The site emergency response team, as well as the local fire
				department, should know the location of all hydrogen storage tanks.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.1.2 Hydrogen Connection Guidelines</h5>
			<ul>
				<li>Use the following guidelines to safely connect hydrogen to your system:</li>
				<li>PipingHydrogen must be delivered to equipment using
				appropriate piping and be done in such a way as to pose essentially
				no hazard to end-users. Piping systems for the delivery of hydrogen
				should be designed and installed by a person qualified by specific
				training and experience with hydrogen piping systems.</li>
				<li>Stainless steel is usually recommended because it is a safe,
				cost-effective material. Piping of black iron must not be used,
				because the pipe can become brittle with age. Elastomeric/plastic
				tubing of various plastics and polymers should not be used, unless the
				tubing is approved for use with hydrogen. If elastomeric/plastic
				tubing is used for hydrogen gas delivery, the tubing should be tested
				for hydrogen permeability to minimize leakage.</li>
				<li>The hydrogen piping system must be flexible enough to endure
				routine thermal expansion and contraction. The system should also
				include considerations for the most severe condition of temperature
				and pressure expected during service. Piping and supports must be
				able to withstand static loading introduced by such things as ice and
				snow; and dynamic loading from high wind and earthquake.</li>
				<li>Use caution should if you bury the hydrogen piping. Proper controls
				should be used to protect against damage and corrosion, and also to
				prevent hydrogen from entering a building if there is any leakage.</li>
				<li>FittingsAll fittings must be approved or designed for use with
				hydrogen gas. Use as few fittings as possible to minimize the
				potential for leaks. After installation, make sure that leak testing is
				carried out prior to system use, and on a regular basis.</li>
				<li>There must be no PTFE tape or other things like plumber's putty used
				to enhance a seal, as this actually is a detriment to a good seal. Ideally,
				the best installation would use stainless steel tubing with appropriate
				gas-tight fittings.</li>
				<li>Welding is usually preferred for joints in hydrogen piping systems
				because welding provides a better connection and reduces the
				potential for leaks compared to mechanical fittings. Soft solder joints
				are not permitted for hydrogen systems (due to the low melting point
				of soft solder and its potential for brittle failure at cryogenic
				temperatures). Brazed joints are permitted, but such joints should be
				protected against the possibility of external fire.</li>
				<li>Tubing connections should be clamped to barbed or press-fit type
				connections. Hose clamps or jubilee clamps must not be used.</li>
				<li>ValvesAll valves must be suitable for hydrogen service and for the
				specific operating conditions. Valves, including regulators, must not
				be used for hydrogen, unless they are designed and identified for
				such a use. Ball valves are often chosen because of their superior leak
				tightness through the valve seat. Pneumatic operators are usually
				chosen for remotely operated valves so that potential ignition sources
				(electricity) are remote from the valve.</li>
				<li>Manual shutoff valves should be provided near each point of use,
				within immediate reach. If a hydrogen cylinder or hydrogen
				generation system is located within immediate reach, a separate
				point-of-use shutoff valve is usually not necessary.</li>
				<li>Line regulators that have their source away from the point of use
				should have a manual shutoff valve near the point of use.</li>
				<li>An emergency gas shutoff device in an accessible location outside the
				use area should be provided in addition to the manual point-of-use
				valve in each educational and instructional laboratory space that has
				a piped gas supply system.</li>
				<li>If necessary, the piping system should have uninterruptible pressure
				relief. The pressure relief system should be designed to provide a
				discharge rate sufficient to avoid further pressure increase and should
				vent to a safe location outside or to a ventilation system exhaust.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.1.3 Purchasing Hydrogen</h5>
			<ul>
				<li>Use the following guidelines when purchasing hydrogen:</li>
				<li>Hydrogen GeneratorBecause it minimizes the amount of hydrogen
				present and reduces the degree of hazard, a hydrogen generator (also
				called an electrolyzer) is the safest way to purchase hydrogen.</li>
				<li>However, to minimize the degree of hazard, the hydrogen generator
				must only be operated in a non-explosive environment because
				hydrogen buildup can be ignitable. This means that your ventilation
				system for the room or lab hood must maintain an air exchange rate
				that is at least two orders of magnitude greater than the maximum
				hydrogen production rate of the hydrogen generator. Follow the
				manufacturers' directions about the correct use and maintenance of
				the regulator.</li>
				<li>To prevent the possibility of releasing hydrogen, the hydrogen
				generator should be set to shut down if:
					<ul>
						<li>There is a loss of flow to the ventilation system.</li>
						<li>A hydrogen detector alarms at 25% of the lower flammable limit
						of hydrogen in air. The oxygen exhausted by the electrolyzer
						should be vented to the outside as well.</li>
					</ul>
				</li>
				<li>Hydrogen CylinderHydrogen can be delivered in standard
				laboratory gas bottles or cylinders. These cylinders have a limited
				amount of hydrogen in them and are a safe way to transport and
				store hydrogen. However, compressed hydrogen gas cylinders, like all
				compressed gas cylinders, must be secured in an upright position,
				ideally with a non-combustible chain or cable. If the cylinder falls
				over, the valve can be knocked off and the pressurized cylinder can
				take off like a rocket, which leads to the release of hydrogen and
				possibly an explosion, severe injury, or death. Do not crack a
				hydrogen cylinder valve to remove dust or dirt from fittings prior to
				attaching a regulator, because there is a risk of self-ignition.</li>
			</ul>

			<hr className='class-2'/>
			<h5>4.1.4 Properly Storing Hydrogen</h5>
			<ul>
				<li>Storing and handling compressed hydrogen gas and cryogenic liquid
				hydrogen present potential health and safety hazards. Using the correct
				storage and handling techniques is essential to maintaining a safe work
				environment.</li>
				<li>Use the following guidelines when storing hydrogen:</li>
				<li>Store spare hydrogen gas cylinders outside and away from doors,
				windows, building air intake vents, structures, and vehicle routes.
				This precaution applies when the hydrogen is or is not in use. Indoor
				storage of spare hydrogen cylinders has special requirements, which
				is beyond the scope of this document. Documentation for each vessel
				should include a description of the vessel, a list of available drawings
				or other documents, the most recent inspection results, and the
				responsible person's name.</li>
				<li>Prevent spare cylinders from toppling by wrapping them with chains.
				The chains should also be protected against corrosion and excessive
				heat.</li>
				<li>Separate spare hydrogen cylinders from oxidizing gases (such as
				oxygen) with a 1.5 m (5 ft) tall fire barrier with a half-hour fire rating
				or place the cylinders at least 6 m (20 ft) apart.</li>
				<li>When you move hydrogen cylinders:
					<ul>
						<li>Remove the regulator and replace the cylinder valve cap before
						moving.</li>
						<li>Move cylinders on cylinder carts or with other appropriate
						transport devices.</li>
						<li>Do not roll or drop a cylinder and do not lift a cylinder by its
						protective cap.</li>
					</ul>
				</li>
				<li>Bulk hydrogen systems include either gaseous or liquid hydrogen in
				fixed installations. In some gas systems, a semi-permanent trailer
				(tube trailer) can be used. Storage vessels for compressed hydrogen
				gas or liquid hydrogen should be designed, constructed, tested, and
				maintained in accordance with applicable codes and standards. Bulk
				hydrogen systems represent a level of complexity again that is beyond
				the scope of this document. However, some general guidelines are
				provided.</li>
				<li>The bulk hydrogen storage system should not be located beneath
				electric power lines, close to other flammable gases/liquids, or close
				to public areas. It should be readily accessible to authorized
				personnel and delivery equipment, but protected from physical
				damage or tampering.</li>
				<li>Because liquid hydrogen systems also have a cryogenic hazard,
				additional safety considerations for the use of cryogenic liquids may
				be necessary.</li>
			</ul>

			<hr className='class-2'/>
			<h5>4.1.5 Hydrogen Safety Codes, Standards and References</h5>
			<ul>
				<li>The following list of safety codes, standards and references is in no way
				an exhaustive list. In fact, there may be federal, state or local codes that
				apply to your specific location. Check with all appropriate agencies with
				jurisdiction before installing or using a hydrogen system.</li>
				<li>Air Products Safetygram #4 Gaseous Hydrogen</li>
				<li>ANSI/AIAA standard for hydrogen safety guidelines is
				AIAA G-095-2004, Guide to Safety of Hydrogen and Hydrogen
				Systems</li>
				<li>ASME B31.1, Power Piping Code</li>
				<li>ASME B31.3, Process Piping Code</li>
				<li>ASME B31.8, Gas Transmission and Distribution Systems</li>
				<li>BCGA Code Of Practice CP4 Industrial Gas Cylinder Manifolds
				and Gas Distribution Pipework</li>
				<li>BCGA Code Of Practice CP33 The Bulk Storage of Gaseous
				Hydrogen at Users' Premises</li>
				<li>CGA G-5, Hydrogen</li>
				<li>CGA G-5.4, Standard for Hydrogen Piping Systems at Consumer
				Locations</li>
				<li>CGA G-5.5, Hydrogen Vent Systems</li>
				<li>CGA G-5.6, Hydrogen Pipeline Systems</li>
				<li>CGA G-5.8, High Pressure Hydrogen Piping Systems at Consumer
				Locations.</li>
				<li>FM Global Property Loss Prevention Data Sheets 7-50: Compressed
				Gases in Cylinders</li>
				<li>FM Global Property Loss Prevention Data Sheets 7-91: Hydrogen</li>
				<li>IGC Doc 121/04/E, Hydrogen Transportation Pipelines System
				Design Features</li>
				<li>NASA</li>
				<li>NSS 1740.16 Safety Standard For Hydrogen And Hydrogen Systems
				Guidelines for Hydrogen System Design, Materials Selection,
				Operations, Storage, and Transportation</li>
				<li>NFPA 52, Vehicular Fuel Systems Code</li>
				<li>NFPA 55, Standard for the Storage, Use, and Handling of
				Compressed Gases and Cryogenic Fluids in Portable and Stationary
				Containers, Cylinders, and Tanks, 2005 Edition</li>
				<li>NFPA 68, Standard on Explosion Protection by Deflagration Venting</li>
				<li>NFPA 70, National Electrical Code</li>
				<li>NFPA 497, Recommended Practice for the Classification of
				Flammable Liquids, Gases, or Vapors and of Hazardous (Classified)
				Locations for Electrical Installations in Chemical Process Areas</li>
				<li>NFPA 13, Standard for the Installation of Sprinkler Systems</li>
				<li>NFPA 45, Standard on Fire Protection for Laboratories Using
				Chemicals</li>
				<li>NFPA 55, Standard for the Storage, Use, and Handling of
				Compressed Gases and Cryogenic Fluids in Portable and Stationary
				Containers, Cylinders, and Tanks</li>
				<li>NFPA 68, 2007 Standard on Explosion Protection by Deflagration
				Venting</li>
				<li>NFPA 69, Standard on Explosion Prevention Systems</li>
				<li>NFPA 91, Standard for Exhaust Systems for Air Conveying of Vapors</li>
				<li>NFPA 255, Standard Method of Test of Surface Burning
				Characteristics of Building Materials</li>
				<li>OSHA 29CFR1910.103 1910.103 Hydrogen</li>
			</ul>

		</div>
	)
}
