import React from 'react';
import '../../styles/styles.css'

function _5_5() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_5;

function MainContents() {
	const title = '5.5 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>5. Safety Instructions for Wearers of Medical Implants</h2>
			<ul>
				<li><img src="../images/operating/ch5_2.jpg" width="auto"  alt=''></img>HF/RF magnetic field emitting instruments
				might influence active and passive body
				implants, such as pacemakers, infusion pumps,
				hip joint replacements, etc.
				<img src="../images/operating/ch5_3.jpg" width="auto"  alt=''></img>If you are wearing a cardiac pacemaker, an implanted heart defibrillator
				or medical implants that might contain ferromagnetic materials (clips or
				prostheses, for example), read and follow these safety instructions.</li>
				<li><img src="../images/operating/ch5_4.jpg" width="auto"  alt=''></img>Magnetic and Electromagnetic Radiation. The electromagnet might
				affect the function of cardiac pacemakers, implanted heart
				defibrillators and medical implants that might contain ferromagnetic
				materials (clips or prostheses, for example). If you wear these devices,
				keep a sufficient safety distance to the magnet.</li>
				<li>Parts of a pump or a gauge emit electromagnetic radiation. This
				radiation can interfere with the operation of cardiac pacemakers and
				implanted heart defibrillators, possibly causing death or serious injury.
				If you wear these devices, keep a sufficient safety distance to the pump
				or gauge.</li>
				<li>Current static magnetic field guidelines restrict exposures for wearers
				of cardiac pacemakers to 0.5 mT. To avoid exposure to stray fields,
				restrict access to personnel with pacemakers or other implanted
				electronic devices to a minimum distance from the edge of the magnet
				coil assemblies.</li>
				<li><img src="../images/operating/ch5_5.jpg" width="auto"  alt=''></img>Medical implants (clips or prostheses, for example) that may contain
				ferromagnetic materials, would be subject to strong attractive forces
				near to the magnet. All people with such implants should be excluded
				from the laboratory rooms and appropriate warning signs be
				displayed.</li>
				<li><img src="../images/operating/ch5_6.jpg" width="auto"  alt=''></img>Strong magnetic fields might affect the operation of electronic
				components. They can cause permanent damage of magnetically
				sensitive devices.</li>
				<li><img src="../images/operating/ch5_7.jpg" width="auto"  alt=''></img>Strong attractive forces can cause metal objects to move uncontrollably
				towards them. Do not bring compressed gas cylinders, tools, or other
				metal objects near to the magnet. Keep computers, credit cards and
				other magnetically sensitive devices sufficiently far away from the
				instrument.</li>
				<li>To avoid hazards from flying metal objects, do not insert items, such as
				tools made of magnetically permeable material, in the gap between
				upper and lower magnet coil assemblies when the magnet circuit is
				switched on.</li>
				<li><img src="../images/operating/ch5_8.jpg" width="auto"  alt=''></img>Read the Operating Manual of your instrument to get comprehensive
				information about the specific hazards that are presented by it. The
				manual also provides information about the necessary
				countermeasures (safety distances, for example).</li>
				<li><img src="../images/operating/ch5_9.jpg" width="auto"  alt=''></img>If not stated otherwise in the Operating Manual, carriers of active
				implants must keep a safety distance of at least 30 cm (12 in) to the
				magnet, magnet coil or pump.</li>
			</ul>

		</div>
	)
}
