import React from 'react';
import '../../styles/styles.css'

function _2_4() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _2_4;

function MainContents() {
	const title = '2-4. Reference and Reagent Gas Inlet';

	return (
		<div>
			<h1 className='title'>{title}</h1>	

			<hr className='class-1'/>

			{/*  */}
			<h2>4. Reference and Reagent Gas Inlet</h2>
			<ul>
				<li>The reference and reagent gas inlet system is a double inlet system and
				consists of two locally separated components:</li>
				<li>Use the reference inlet region of the system to introduce the reference
				sampleperfluorokerosene (PFK), for examplefor mass
				calibration or MID measurements. The inlet system itself is on top
				of the ion source head and can be reached from the front side. See
				below.</li>
				<li>Use the reagent gas inlet region of the system to introduce the reagent
				gas for chemical ionization. With the Advanced CI Kit installed,
				containers for two CI reagent gases (methane, isobutane, or
				ammonia) can be connected to a manifold. Through a set of valves,
				you can select the reagent gas type and introduce the gas into the ion
				source. The reagent gas manifold is on the power column at the rear
				instrument side. (See Figure 2-11 on page 2-15.)</li>
				<li>Parts of the reference and reagent inlet system are heated during
				operation. The temperature is set with the DFS Tune software. The
				temperature range is approximately ambient to 250 C. Refer to the
				DFS Software Manual for more information. No temperature readout
				value is displayed.</li>
			</ul>

			<hr className='class-2'/>
			<h4>4.1 Reference Inlet Region</h4>
			<ul>
				<li>The liquid or gaseous reference sample is injected into the volume of the
				reference inlet region through a septum in the injection port. The
				septum is to be put on the injection port on top of the system with the
				Teflon-coated side downward. The septum is held and sealed on the
				injection port by a cap screw. See Figure 2-23. The syringe to be used
				should be a GC syringe and the typical reference sample size should be
				approximately 13 L. See page 7-12 instructions about inserting the
				reference compound.</li>
				<li><img src="../images/operating/ch2_27.jpg" width="auto"  alt=''></img><br/></li>
				<li>Because liquid reference samples are injected through the injection port,
				no reference gas vials exist. Reference gases occur only if these liquid
				reference samples are evaporated.</li>
				<li>Use the two adjusting knobs to control the inflow of each gas type
				(reference gas on the left, and reagent gas on the right). Both dosing
				valves close when they are turned clockwise, and open when they are
				turned counterclockwise.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>4.2 Valve Schematics</h4>
			<ul>
				<li>Figure 2-24 shows a schematic diagram of the reference and reagent gas
				inlet. The left side shows parts of the inlet system with the manually
				operated dosing valves (REF for reference gas, and CI for reagent gas)
				and the solenoid operated pumping valves (VV and VVR). The solenoid
				operated valves are operated with the DFS Tune software. Refer to the
				DFS Software Manual for more information. The upper range of the
				right side shows a representation of the reference gas inlet.</li>
				<li><img src="../images/operating/ch2_28.jpg" width="auto"  alt=''></img><br/></li>
				<li>The flow of the reference compound from the reference volume to the
				ion source is controlled by the manually operated dosing valve REF (left
				adjusting knob on Figure 2-23). This valve can be precisely set to the
				correct inlet rate with the rotary knob. The inlet rate is checked by
				reading the high vacuum pressure in the source head. In an instrument
				without a GC connected, the pressure for typical inlet rates is between
				10-7 and 10-6 mbar. The REF inlet valve can also be used to completely
				shut off the reference flow. The reference volume can be pumped to
				forevacuum by opening the solenoid operated valve VVR. This valve is
				connected to the changeover valve of the source forepump line.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>4.3 CI Gas Inlet Region</h4>
			<ul>
				<li>In the standard version of the instrument, you can connect one reagent
				gas cylinder to the reagent gas manifold. The CI gas 1 inlet port is at the
				power panel on the rear side of the instrument. See Figure 2-11 on
				page 2-15. The CI reagent gas enters the reference and reagent gas inlet
				at the right side. See Figure 2-25.</li>
				<li><img src="../images/operating/ch2_29.jpg" width="auto"  alt=''></img><br/></li>
				<li>The reagent gas region can be pumped to forevacuum through the
				solenoid operated valve VV, which is connected to the source forepump
				line. You can adjust the reagent gas flow into the CI boxthe
				CI volume of the ion sourceby the manually operated dosing valve CI
				(right adjusting knob on Figure 2-23 on page 2-31).</li>
				<li>The valves VVR and VV (see Figure 2-25) are electrically operated
				solenoid valves and are mounted separately from the heated central
				block. Therefore, when the central block is heated, the temperature of
				these valves is considerably smaller than the temperature of the block.
				The forevacuum valves VVR and VV are on the left side (seen from
				front side) and are connected to the forevacuum manifold for the inlet
				systems. The valve VV switches the reagent gas flow on or off. The
				valve VVR is used to pump away reference gas. Both valves are switched
				with the DFS Tune software. Refer to the DFS Software Manual for
				more information.</li>
				<li>When the source is operated continuously in the CI mode, VV is closed.
				During continuous EI operation, this valve is also closed. When
				switching from CI to EI mode, VV is opened several times for a short
				time interval.</li>
			</ul>

			<hr className='class-2'/>
			<h5>4.3.1 Advanced CI Kit</h5>
			<ul>
				<li>Figure 2-24 on page 2-32 shows also a schematic of the optional
				Advanced CI Kit. It is required for operating the instrument with two
				reagent gases and contains connectors for two reagent gas cylinders as
				well as the solenoid separation valves VS2, VS3, and VG. Reagent gas
				from one of the containers can be selected by the solenoid operated
				valves VS2 (for CI reagent gas 1) or VS3 (for CI reagent gas 2). From
				these valves, the reagent gas flows through the solenoid operated
				separation valve VG into the reagent gas region of the inlet system. The
				reagent gas shut-off valve VG is on the right side (seen from front side)
				and is connected to the reagent gas manifold.</li>
				<li>All three valves are switched with the DFS Tune software. Refer to the
				DFS Software Manual for more information. The simultaneous opening
				of valves VV and VG is blocked, if one of the valves VS2 or VS3 is open.
				This is to prevent the evacuation of the reagent gas containers by
				mistake.</li>
				<li><img src="../images/operating/ch2_30.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>4.4 Design of the Inlet System</h4>
			<ul>
				<li>The inlet system consists of a heated central block (in Figure 2-24
				labeled as heated area) containing all inlet lines, volumes, and valves.
				Two heaters, inserted into holes at the rear side of the central block, heat
				the block to a maximum temperature of up to 240 C. To prevent
				damage to the sealing of the system, this temperature is too high to be
				applied as permanent heating temperature. The maximum permissible
				temperature for permanent heating is approximately 160 C.</li>
				<li>The heater of the reference inlet is controlled by the power distribution
				board. The heating power is stopped and set to a value between 0% and
				100% with the DFS Tune software. Refer to the DFS Software Manual
				for more information. It is not necessary for the heater to be working to
				full capacity. A typical time for heating the reference and reagent gas
				inlet system from room temperature to the typical operating
				temperature of approximately 150 C is approximately 1 hour.</li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.4.1 CI Reagent Gas Inlet Ports</h5>
			<ul>
				<li>The inlet ports of the reagent gas manifold are at the power column on
				the rear side of the instrument. See Figure 2-11 on page 2-15. The
				manifold ends in two 1/8 inch Swagelok fittings. The 1/8 inch
				connection tubes, the reduction valves and the reagent gas containers
				must be provided by the customer.</li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.4.2 Common Inlet Line</h5>
			<ul>
				<li>The reference compound and the CI reagent gas are admitted to the ion
				source through a common inlet line. See Figure 2-27.</li>
				<li><img src="../images/operating/ch2_31.jpg" width="auto"  alt=''></img><br/></li>
				<li>This line consists of a central fused silica capillary and a surrounding
				copper tube. The tube is in thermal contact with the heated central
				block, and thus acts as a heat transfer line to the capillary. Inside the
				source head, the fused silica capillary ends in a connection line. The line
				is gas-tight connected to the reference and reagent gas inlet port on the
				upper side of the ion source block.</li>
				<li>The gas streams from the reference inlet through the capillary. Then the
				connection line transfers the gas to the reference and reagent gas inlet
				port on the ion source block. From here, a channel leads to the right
				inlet hole of the ion source block and ends between the inner and the
				outer sealing cylinders. See below.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h5>4.4.3 Sample Inlet Ports of the Ion Source</h5>
			<ul>
				<li>Figure 2-28 shows a cross section of the ion source with the sample inlet
				ports as seen from the instrument front side.</li>
				<li><img src="../images/operating/ch2_32.jpg" width="auto"  alt=''></img><br/></li>
				<li>Figure 2-29 shows the ion source with inserted probe rod and GC
				transfer line. The ion source block has inlet holes on both sides. Each
				hole contains an inner and an outer sealing cylinder. Both cylinders slide
				within the hole. The outer cylinder is stopped in its final position by a
				stopping device that is screwed to the ion source block. The inner
				cylinder is stopped by a recess within the inlet hole. Both cylinders are
				pressed in opposite directions by a spring.</li>
				<li>If the rod of the direct evaporation probe is introduced through the
				vacuum lock into operating position, or if the inlet line of the GC
				interface is screwed to the right side of the source head, then the sealing
				surface of the rod or of the inlet line is pressed against the sealing surface
				of the outer cylinder. Thus, the lines are gas-tight with respect to the
				surrounding volume of the source head. See also Chapter 3, Direct
				Evaporation System.</li>
				<li>If one of the ionization boxes is introduced into the ion source block,
				then the sealing surfaces of the inner cylinders are tightly pressed against
				the sealing surface of the ionization box. Thus, a gas-tight inlet from
				both sides is provided.</li>
				<li>Tip Occasionally, the flow of the reference substance into the ion
				source can no longer be properly controlled at the reference inlet
				system. Then call a Thermo Fisher Scientific field service engineer to
				perform maintenance.</li>
				<li><img src="../images/operating/ch2_33.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
			</ul>

			<hr className='class-1'/>

		</div>
	)
}
