import React from 'react';
import '../../styles/styles.css'

function _4_1() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _4_1;

function MainContents() {
	const title = '4.1 GC/MS Interface';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>


			{/*  */}
			<h2>1. Introduction to the GC/MS Interface</h2>
			<ul>
				<li>This section describes the GC/MS interface system, which couples gas
				chromatographs to the mass spectrometer. Mechanically, the GC is
				coupled to the ion source by the standard direct coupling line.
				Theoretically, each gas chromatograph can be connected to the mass
				spectrometers. In praxis, the hardware coupling parts and the software
				processor control of the mass spectrometers are provided for these
				GC models:</li>
				<li>Thermo Scientific TRACE GC Ultra, TRACE 1300 Series GC</li>
				<li>The GC/MS interface device consist of these components:</li>
				<li>GC. For all models, the manufactures have provided detailed
				manuals, and books have been written on gas chromatography. This
				manual, therefore, will have no functional descriptions of the gas
				chromatographs and no general section on gas chromatography.</li>
				<li>Transfer Line, connecting the outlet of the GC column with the
				inlet of the ion source of the mass spectrometer. See page 4-5.</li>
				<li>Direct Coupling Parts. These are the standard coupling parts for
				the coupling between the gas chromatographic column and the
				transfer line. They are described below.</li>
				<li>Autosampler. The autosampler is an optional accessory to the gas
				chromatograph. Various models can be used. Please ask the Thermo
				Fisher Scientific representative for detailed information.
				Descriptions of the autosamplers can be found in the manuals of the
				manufacturers.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>1.1 Communication and Power Supply for GCs and Autosampler</h4>
			<ul>
				<li>Power supply ports for GCs and autosampler are at the rear side of the
				DFS Magnetic Sector GC-HRMS system. The handshake cables of the
				GCs are connected to the digital input port at the rear side of the
				instrument. See Interconnections of GC/MS Systems on page 6-21
				for details.</li>
				<li>Each GC has its own local microprocessor for temperature control of
				the injector block, oven, and transfer line. With the Xcalibur software
				on the data system, you control the DFS mass spectrometer, the
				TRACE 1300 Series GC, and the TriPlus autosampler, as well as other
				devices. Refer to the DFS Software Manual for details.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>1.2 GC Table</h4>
			<ul>
				<li>In the standard configuration of the DFS mass spectrometer, the
				GC table is at the right side of the source head. See Figure 4-1. If the
				mass spectrometer is to be used with two GCs (Dual GC option), then a
				similar GC table can be mounted to the left side of the source head. See
				Figure 4-7 on page 4-9</li>
				<li><img src="../images/operating/ch4_1.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>1.3 Gas Supply for Gas Chromatographs</h4>
			<ul>
				<li>Helium is used as GC carrier gas. Connect the helium supply in the
				laboratory to the inlet port on the power column at the rear side of the
				DFS mass spectrometer. (See Figure 2-11 on page 2-15.) Inside the
				instrument, the gas is led to outlet ports on both sides of the ion source
				head, below the GC table(s). Figure 4-2 shows the helium port on the
				left side of the source head. Connect the gas pipe of the GC(s) here.</li>
				<li><img src="../images/operating/ch4_2.jpg" width="auto"  alt=''></img><br/></li>
				<li><img src="../images/operating/ch4_3.jpg" width="auto"  alt=''></img><br/></li>
				<li></li>
				<li></li>
				<li></li>
			</ul>


		</div>
	)
}
