import React from 'react';
import '../../styles/styles.css'

function _5_8() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_8;

function MainContents() {
	const title = '5.8 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>8. Personal Protective Equipment</h2>
			<ul>
				<li>This manual can only give general suggestions for personal protective
				equipment (PPE), which protects the wearer from hazardous substances.
				Refer to the Material Safety Data Sheets (MSDSs) of the chemicals
				handled in your laboratory for advice on specific hazards or additional
				equipment.</li>
			</ul>

			<hr className='class-2'/>
			<h4>8.1 Eye Protection</h4>
			<ul>
				<li>The type of eye protection required depends on the hazard. For most
				situations, safety glasses with side shields are adequate. Where there is a
				risk of splashing chemicals, goggles are required.</li>
			</ul>

			<hr className='class-2'/>
			<h4>8.2 Protective Clothing</h4>
			<ul>
				<li>When the possibility of chemical contamination exists, protective
				clothing that resists physical and chemical hazards should be worn over
				street clothes. Laboratory coats are applicable for small chemical splashes
				and solids contamination, while plastic or rubber aprons are best for
				protection from corrosive or irritating liquids.</li>
			</ul>

			<hr className='class-2'/>
			<h4>8.3 Gloves</h4>
			<ul>
				<li>For handling chemical compounds and organic solvents, we recommend
				white nitrile clean room gloves from Fisher Scientific or Unity Lab
				Services.</li>
				<li>For handling hot objects, gloves made of heat-resistant materials (for
					example, leather) should be available.</li>
			</ul>

			<hr className='class-2'/>
			<h4>8.4 Safety Boots and Shoes</h4>
			<ul>
				<li>Safety boots and shoes, which are penetration-resistant and feature
				protective toecaps, can protect operating personnel from falling objects,
				vehicles and other heavy loads, for example when moving the
				instrument.</li>
			</ul>

		</div>
	)
}
