import React from 'react';
import '../../styles/styles.css'

function _5_3() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _5_3;

function MainContents() {
	const title = '5.3 Safety';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>3. Intended Use</h2>
			<ul>
				<li>The DFS mass spectrometer is a double-focusing sector field instrument
					with reverse Nier-Johnson geometry. It is designed for highly specific
					and ultra-sensitive target compound quantification in environmental,
					sports doping, and other target compound analysis.</li>
				<li>Observe these usage guidelines when you operate your mass
				spectrometer:</li>
				<li>The instrument is designed to be placed in a laboratory. It is not
				designed for the use outdoors.</li>
				<li>The instrument is designed for laboratory research use only. It is
				not designed for use in diagnostic or medical therapeutic
				procedures.</li>
				<li>If the instrument is used in a manner not specified by Thermo Fisher
				Scientific, then the protection provided by the instrument could be
				impaired. Thermo Fisher Scientific assumes no responsibility and will
				not be liable for instrument damage and/or operator injury.</li>
				<li>Notice on the Susceptibility to Electromagnetic Transmissions</li>
				<li>Your instrument is designed to work in a controlled electromagnetic
				environment. Do not use radio frequency transmitters, such as mobile
				phones, in close proximity to the instrument.</li>
			</ul>

			<hr className='class-2'/>
			<h4>3.1 Qualification of the Personnel</h4>
			<ul>
				<li>Personnel that install or operate the DFS mass spectrometer must have
				these qualifications:</li>
				<li>Electrical Connections<br/>
						The electrical installation must be carried out by qualified and
						skilled personnel (electrician) according to the applicable
						regulations (for example, cable cross-sections, fuses, PE
						connection). Refer to the DFS Pre-Installation Requirements Guide
						for the specifications.</li>
				<li>Installation<br/>
						Only employees of Thermo Fisher Scientific or personnel acting
						on behalf of Thermo Fisher Scientific are allowed to install the
						mass spectrometer.</li>
				<li>General Operation<br/>
						The mass spectrometer is designed to be operated by qualified
						laboratory personnel. Before starting, all users must be instructed
						about the hazards presented by the instrument and the used
						chemicals. The users must be advised to read the relevant Material
						Safety Data Sheets (MSDSs).</li>
				<li>Decommissioning<br/>
						Only employees of Thermo Fisher Scientific or personnel acting
						on behalf of Thermo Fisher Scientific are allowed to
						decommission the mass spectrometer.</li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>3.2 Permitted Materials</h4>
			<ul>
				<li>The DFS mass spectrometer is designed to be operated with these
				materials:</li>
				<li>
					<ul>Compressed air: Used for pneumatic control of these components:
						<li>Separation valve</li>
						<li>Box exchange lock</li>
						<li>Probe exchange (DI) lock</li>
						<li>Vacuum changeover valve</li>
					</ul>
				</li>
				<li>Nitrogen, helium, or argon gas: Used to vent the instrument</li>
				<li>
					<ul>Distilled water: Used for cooling these instrument parts:
						<li>Magnet</li>
						<li>Turbomolecular pumps</li>
						<li>Magnetic field probe</li>
						<li>DI devices or ESI</li>
					</ul>
				</li>
				<li>Forepump oil: Used for cooling, lubrication, and sealing of the
				forepumps</li>
				<li>NOTICE<br/>
					 Use only the forepump oil indicated on the name plate of the
						pump. If other oils are used, the manufacturer (Pfeiffer) rejects all
						responsibility should any trouble occur.</li>
				<li>Calibration compounds, samples</li>
				<li>CAUTION <br/>
					Hazardous Chemicals. Samples and calibration compounds
					might contain toxic, carcinogenic, mutagenic, or corrosive/irritant
					chemicals. Always wear protective clothing, gloves, and safety
					glasses when you handle these compounds. Refer to your supplier's
					Material Safety Data Sheet (MSDS) for the correct handling of a
					particular compound.</li>
				<li>Solvents</li>
				<li>When you operate the mass spectrometer with gas chromatographs,
				refer to the manuals that came with the GCs for the specifications of the
				GC carrier gas.</li>
				<li>When you operate the mass spectrometer in an ionization mode other
				than electron impact ionization (CI or API, for example), it may be
				necessary to use additional materials. Refer to the relevant
				documentation for these applications. See page 6-10 for information
				about the required gases.</li>
			</ul>

		</div>
	)
}
