import React from 'react';
import '../../styles/styles.css'

function _6_0() {
	return (
		<>
			<div className="px-4" style={{marginTop:80}}>
					<MainContents />
			</div>
		</>
	);
};
export default _6_0;

function MainContents() {
	const title = '6.7 Installation';

	return (
		<div>
			<h1 className='title'>{title}</h1>	
			<hr className='class-1'/>

			{/*  */}
			<h2>7. Setting Up the Instrument Hardware and Adjusting the System Parameters</h2>
			<ul>
				<li>The mass spectrometer and other devices that are shipped with it are
				installed by a Thermo Fisher Scientific field service engineer. During the
				installation, the service engineer will demonstrate the basics of
				equipment operation and routine maintenance.</li>
				<li>For detailed information about changing the instrument parameters and
				developing experiments for the instrument, refer to the DFS Software
				Manual or the DFS Tune Help.</li>
			</ul>

			<hr className='class-2'/>
			<h4>7.1 DFS Chemicals and Calibration Standards Kit</h4>
			<ul>
				<li>Before the instrument arrives, you will receive the DFS Preinstallation
				Kit in a separate shipment. The kit contains a box with the
				DFS Chemicals and Calibration Standards Kit, the dioxin standard, and
				the wall receptacle. It is your responsibility to have the wall receptacle
				installed by your electrician before the instrument installation starts.</li>
				<li>The box with the DFS Chemicals and Calibration Standards Kit is
				sealed and labeled with a caution mark. Do not open it! Your Thermo
				Fisher Scientific field service engineer will do this.</li>
			</ul>

			<hr className='class-2'/>
			<h4>7.2 Interconnections of GC/MS Systems </h4>
			<ul>
				<li>This topic outlines the connections between DFS mass spectrometer,
				data system, TRACE GC(s), and TriPlus autosampler:</li>
				<li>Autosampler: Connect the 230 V power cable of the power supply
				to the multiple socket power connector. See Figure 6-6 on
				page 6-24. Connect the power cable between the power supply and
				the sampler supply connector of the autosampler. Connect all
				accessories to the bus system.</li>
				<li>Data system: Connect the autosampler and the GCs by an RS-232
				interface. Connect the mass spectrometer with the network cable to
				the data system. Use always the onboard network connector of the
				data system for the mass spectrometer.</li>
				<li>GC(s): Connect the 230 V power cable(s) of the GC(s) to the
					Peripheral Devices outlets on the rear side of the mass spectrometer.
					Connect the He lines from the injectors on the rear side of each GC
					to the He connectors of the mass spectrometer below the GC tables.
					See Figure 4-2 on page 4-3.</li>
				<li>Single GC Configuration: Connect the handshake connector
				Main (GC1) on the autosampler side to the autosampler
				connector (AS) on the GC side. Connect the generic handshake
				connector (GH) on the GC side to the Digital Input connector
				on the mass spectrometer side.</li>
				<li><img src="../images/operating/ch6_9.jpg" width="auto"  alt=''></img></li>
				<li>Dual GC Configuration: Connect the cable 2091800 to the
				handshake connector Main (GC1), the aux-signals connector on
				the autosampler side, and to the autosampler connectors (AS) on
				the GC's sides. Connect the cable 2091790 to the generic
				handshake connectors (GH) on the GC's sides and the Digital
				Input connector on the mass spectrometer side. See Figure 2-9
				on page 2-12. Both cables are shipped with the Dual GC Kit.</li>
				<li><img src="../images/operating/ch6_10.jpg" width="auto"  alt=''></img></li>
				<li></li>
			</ul>

			<hr className='class-2'/>
			<h4>7.2 Power Supply for GC/MS Systems</h4>
			<ul>
				<li>The DFS mass spectrometer can supply power for up to four options by
				the power column at the rear side. A multi socket power connector is
				supplied with the instrument for the connection of optional equipment.
				Connect it to the Autosampler power outlet at the power column. See
				Figure 2-9 on page 2-12.</li>
				<li>Thermo Scientific TRACE 1300 Series GCs have an additional
				230 V AC power cable for the transfer line heating. Table 6-5 shows a
				power supply setup for two possible configurations of a DFS instrument
				with Thermo Scientific TRACE 1300 Series GCs and autosampler. See
				also Figure 6-6.</li>
				<li><img src="../images/operating/ch6_11.jpg" width="auto"  alt=''></img></li>
				<li><img src="../images/operating/ch6_12.jpg" width="auto"  alt=''></img></li>
				<li>Tip The external modules of the GC must be powered at the same
				power supply voltage that is required for the GC.</li>
			</ul>

		</div>
	)
}
